import React from 'react';

import { EquipmentBase } from '../../../../models/Diablo2Models';

import { Diablo2EquipmentBaseDataService, useDiablo2EquipmentBaseDataService } from '../../../../services/Diablo2EquipmentBaseDataService';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../services/Diablo2LocalizationService';

import DataEquipmentBaseListCardProp from './DataEquipmentBaseListCardProp';
import DataEquipmentBaseListCardPropBlock from './DataEquipmentBaseListCardPropBlock';
import DataEquipmentBaseListCardPropBlockForOneClass from './DataEquipmentBaseListCardPropBlockForOneClass';
import DataEquipmentBaseListCardPropKeepZero from './DataEquipmentBaseListCardPropKeepZero';
import DataEquipmentBaseListCardPropClassSpeed from './DataEquipmentBaseListCardPropClassSpeed';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();
const diablo2EquipmentBaseDataService: Diablo2EquipmentBaseDataService = useDiablo2EquipmentBaseDataService();

export default function DataEquipmentBaseListCard(input: { equipmentBase: EquipmentBase }) {
    return (
        <div className="card bg-content text-content">
            <div className="card-body" style={{ textAlign: "center" }}>
                <div className="row" style={{ marginBottom: "20px" }}>
                    <div className="col-12">
                        <img
                            src={diablo2EquipmentBaseDataService.getEquipmentBaseImageUrl(input.equipmentBase.normal.image)}
                            alt={input.equipmentBase.normal.key}
                            className='base-equipment-image'/>
                    </div>
                </div>
                <div className="row" style={{ color: "#057af0" }}>
                    <div className="col-5"></div>
                    <div className="col-2">
                        <b>Bnh thng</b>
                    </div>
                    <div className="col-2">
                        <b>Xut sc</b>
                    </div>
                    <div className="col-2">
                        <b>Tinh ty</b>
                    </div>
                </div>

                {/* Name */}
                <hr style={{ backgroundColor: "white" }} />
                <DataEquipmentBaseListCardProp
                    title='Tn'
                    normalValue={diablo2LocalizationService.getEquipmentName(input.equipmentBase.normal.nameKey)}
                    exceptionalValue={diablo2LocalizationService.getEquipmentName(input.equipmentBase.exceptional.nameKey)}
                    eliteValue={diablo2LocalizationService.getEquipmentName(input.equipmentBase.elite.nameKey)}
                    textColor="white" />


                <hr style={{ backgroundColor: "white" }} />
                <DataEquipmentBaseListCardProp
                    title='Phng th'
                    normalValue={input.equipmentBase.normal.defense}
                    exceptionalValue={input.equipmentBase.exceptional.defense}
                    eliteValue={input.equipmentBase.elite.defense}
                    textColor="white" />
                {input.equipmentBase.normal.type === "Shield" ?
                    <DataEquipmentBaseListCardPropBlock
                        normalValue={input.equipmentBase.normal.block}
                        exceptionalValue={input.equipmentBase.exceptional.block}
                        eliteValue={input.equipmentBase.elite.block}
                        textColor="white" />
                    : input.equipmentBase.normal.type === "Shield-Necromancer" ?
                        <DataEquipmentBaseListCardPropBlockForOneClass
                            class='Nec'
                            normalValue={input.equipmentBase.normal.block}
                            exceptionalValue={input.equipmentBase.exceptional.block}
                            eliteValue={input.equipmentBase.elite.block}
                            textColor="white" />
                        : <DataEquipmentBaseListCardPropBlockForOneClass
                            class='Pal'
                            normalValue={input.equipmentBase.normal.block}
                            exceptionalValue={input.equipmentBase.exceptional.block}
                            eliteValue={input.equipmentBase.elite.block}
                            textColor="white" />
                }
                <DataEquipmentBaseListCardProp
                    title='St thng '
                    normalValue={input.equipmentBase.normal.kickDamage}
                    exceptionalValue={input.equipmentBase.exceptional.kickDamage}
                    eliteValue={input.equipmentBase.elite.kickDamage}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='St thng p'
                    normalValue={input.equipmentBase.normal.smiteDamage}
                    exceptionalValue={input.equipmentBase.exceptional.smiteDamage}
                    eliteValue={input.equipmentBase.elite.smiteDamage}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='St thng nm'
                    normalValue={input.equipmentBase.normal.throwDamage}
                    exceptionalValue={input.equipmentBase.exceptional.throwDamage}
                    eliteValue={input.equipmentBase.elite.throwDamage}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='St thng mt tay'
                    normalValue={input.equipmentBase.normal.oneHandDamage}
                    exceptionalValue={input.equipmentBase.exceptional.oneHandDamage}
                    eliteValue={input.equipmentBase.elite.oneHandDamage}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='St thng mt tay (Ngi Man Di)'
                    normalValue={input.equipmentBase.normal.oneHandDamageForBar}
                    exceptionalValue={input.equipmentBase.exceptional.oneHandDamageForBar}
                    eliteValue={input.equipmentBase.elite.oneHandDamageForBar}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='St thng hai tay'
                    normalValue={input.equipmentBase.normal.twoHandDamage}
                    exceptionalValue={input.equipmentBase.exceptional.twoHandDamage}
                    eliteValue={input.equipmentBase.elite.twoHandDamage}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title=' bn'
                    normalValue={input.equipmentBase.normal.durability}
                    exceptionalValue={input.equipmentBase.exceptional.durability}
                    eliteValue={input.equipmentBase.elite.durability}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='S lng ti a'
                    normalValue={input.equipmentBase.normal.maxQuantityStack}
                    exceptionalValue={input.equipmentBase.exceptional.maxQuantityStack}
                    eliteValue={input.equipmentBase.elite.maxQuantityStack}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='Lp nhn vt'
                    normalValue={diablo2LocalizationService.getOnlyClassModifier(input.equipmentBase.normal.onlyClass)}
                    exceptionalValue={diablo2LocalizationService.getOnlyClassModifier(input.equipmentBase.exceptional.onlyClass)}
                    eliteValue={diablo2LocalizationService.getOnlyClassModifier(input.equipmentBase.elite.onlyClass)}
                    textColor="#ff7353" />
                <DataEquipmentBaseListCardProp
                    title='Yu cu sc mnh'
                    normalValue={input.equipmentBase.normal.strengthRequired}
                    exceptionalValue={input.equipmentBase.exceptional.strengthRequired}
                    eliteValue={input.equipmentBase.elite.strengthRequired}
                    textColor="#a55806" />
                <DataEquipmentBaseListCardProp
                    title='Yu cu nhanh nhn'
                    normalValue={input.equipmentBase.normal.dexterityRequired}
                    exceptionalValue={input.equipmentBase.exceptional.dexterityRequired}
                    eliteValue={input.equipmentBase.elite.dexterityRequired}
                    textColor="#00c800" />
                <DataEquipmentBaseListCardProp
                    title='Yu cu cp'
                    normalValue={input.equipmentBase.normal.clvl}
                    exceptionalValue={input.equipmentBase.exceptional.clvl}
                    eliteValue={input.equipmentBase.elite.clvl}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='S l ngc ti a'
                    normalValue={input.equipmentBase.normal.maxSocket}
                    exceptionalValue={input.equipmentBase.exceptional.maxSocket}
                    eliteValue={input.equipmentBase.elite.maxSocket}
                    textColor="#2bd999" />
                <DataEquipmentBaseListCardProp
                    title='S  tht lng'
                    normalValue={input.equipmentBase.normal.maxPotionSlot}
                    exceptionalValue={input.equipmentBase.exceptional.maxPotionSlot}
                    eliteValue={input.equipmentBase.elite.maxPotionSlot}
                    textColor="#2bd999" />

                <hr style={{ backgroundColor: "white" }} />
                <DataEquipmentBaseListCardPropKeepZero
                    title='Tc  c bn'
                    normalValue={input.equipmentBase.normal.baseSpeed}
                    exceptionalValue={input.equipmentBase.exceptional.baseSpeed}
                    eliteValue={input.equipmentBase.elite.baseSpeed}
                    textColor="#c7b377" />
                <DataEquipmentBaseListCardPropClassSpeed
                    normalValue={input.equipmentBase.normal.classSpeed}
                    exceptionalValue={input.equipmentBase.exceptional.classSpeed}
                    eliteValue={input.equipmentBase.elite.classSpeed}
                    textColor="#ffa800" />
                <DataEquipmentBaseListCardProp
                    title='Phm vi tn cng tng cng'
                    normalValue={input.equipmentBase.normal.rangeAdder}
                    exceptionalValue={input.equipmentBase.exceptional.rangeAdder}
                    eliteValue={input.equipmentBase.elite.rangeAdder}
                    textColor="white" />
                <DataEquipmentBaseListCardProp
                    title='Cp cht lng'
                    normalValue={input.equipmentBase.normal.qlvl}
                    exceptionalValue={input.equipmentBase.exceptional.qlvl}
                    eliteValue={input.equipmentBase.elite.qlvl}
                    textColor="#ffff64" />
                <DataEquipmentBaseListCardProp
                    title='Trng lng'
                    normalValue={diablo2LocalizationService.getEquipmentWeight(input.equipmentBase.normal.weight)}
                    exceptionalValue={diablo2LocalizationService.getEquipmentWeight(input.equipmentBase.exceptional.weight)}
                    eliteValue={diablo2LocalizationService.getEquipmentWeight(input.equipmentBase.elite.weight)}
                    textColor="white" />
            </div>
        </div>
    );
}