import React from 'react';

import EquipmentBaseName from '../../../../../../compoments/diablo2/build/EquipmentBaseName';

import RunewordName from '../../../../../../compoments/diablo2/build/RunewordName';
import RunewordFullInfo from '../../../../../../compoments/diablo2/build/RunewordFullInfo';

import SkillName from '../../../../../../compoments/diablo2/build/SkillName';

export default function MercenaryWeapon() {
    return (
        <div id="equipment_mer_weapon_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_mer_weapon">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">V Kh</h4>
                    </div>
                </a>
                <div id="collapse_mer_weapon" className="collapse skill-panel-content text-content" data-parent="#equipment_mer_weapon_accordion">
                    <div className="card-body">
                        <p>
                            Lnh nh thu chng V c kh nng s dng 2 v kh cng lc. y cng chnh l l do bn nn s dng lnh nh thu ny. Bn c th trang b cho n 2 thanh <RunewordName runewordKey='LastWish' />  c c k nng ho quang <SkillName skillKey='Might' /> cp 34 (cng 2 v kh li), ho quang ny s gip bn c c mt lng st thng v cng ln, ngoi ra <SkillName skillKey='LifeTap' /> cng gip bn d th hn khi i Uber. ng thi k nng <SkillName skillKey='Fade' /> ca <RunewordName runewordKey='LastWish' /> cng gip tng kh nng sng st ca lnh nh thu.
                        </p>
                        <p>
                            Hoc bn c th trang b 1 thanh <RunewordName runewordKey='Grief' /> + 1 thanh <RunewordName runewordKey='LastWish' /> hoc cng c th s dng 1 thanh <RunewordName runewordKey='Kingslayer' /> + 1 thanh <RunewordName runewordKey='Lawbringer' />
                        </p>

                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='LastWish' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Grief' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Kingslayer' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Lawbringer' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}