import React from 'react';

import EquipmentUniqueName from '../../../../../../compoments/diablo2/build/EquipmentUniqueName';
import EquipmentUniqueFullInfo from '../../../../../../compoments/diablo2/build/EquipmentUniqueFullInfo';

export default function EquipmentAmulet() {
    return (
        <div id="equipment_amulet_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_amulet">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">Dy Chuyn</h4>
                    </div>
                </a>
                <div id="collapse_amulet" className="collapse skill-panel-content text-content" data-parent="#equipment_amulet_accordion">
                    <div className="card-body">
                        <p>
                            <EquipmentUniqueName uniqueKey='MarasKaleidoscope' /> c dng <span className='text-white'>+2 tt c k nng</span> v <span className='text-white'>tt c khng</span>, 2 th v cng cn thit vi mt n S Druid Nguyn T La. Hoc bn cng c th tm mt <span className='text-rare font-weight-bold'>dy chuyn him</span> mnh mnh vi <span className='text-white'>+3 cc k nng trong bng 'Nguyn T'</span>.
                        </p>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='MarasKaleidoscope' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}