import React from 'react';

import { EquipmentUniqueFilterRequest } from '../../../../../services/Diablo2EquipmentUniqueDataService';

export default function DataEquipmentUniqueFilterByName(input: {
    filter: EquipmentUniqueFilterRequest,
    updateFilter: (filter: EquipmentUniqueFilterRequest) => void
}) {
    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    <label>Tn</label>
                    <input className="form-control" type="text" placeholder="Tn trang b, kha,..." value={input.filter.keyword} onChange={event => {
                        input.filter.keyword = event.target.value;
                        input.updateFilter(input.filter);
                    }} />
                </div>
            </div>
        </div>
    )
}