import React from 'react';

import EquipmentUniqueName from '../../../../../../compoments/diablo2/build/EquipmentUniqueName';
import EquipmentUniqueFullInfo from '../../../../../../compoments/diablo2/build/EquipmentUniqueFullInfo';
import EquipmentSetPartName from '../../../../../../compoments/diablo2/build/EquipmentSetPartName';
import EquipmentSetPartFullInfo from '../../../../../../compoments/diablo2/build/EquipmentSetPartFullInfo';

export default function EquipmentBoot() {
    return (
        <div id="equipment_boot_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_boot">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">Giy</h4>
                    </div>
                </a>
                <div id="collapse_boot" className="collapse skill-panel-content text-content" data-parent="#equipment_boot_accordion">
                    <div className="card-body">
                        <p>
                            Khng c i giy no c xem l ph hp nht vi n S Druid Nguyn T La, ty theo tnh hung m bn s c nhng la chn khc nhau. <EquipmentUniqueName uniqueKey='SandstormTrek' /> s tng mt lng ln <span className='text-white'>phng th</span> v <span className='text-white'>khng c</span>. <EquipmentSetPartName setKey='AldursWatchtower' setPartKey='AldursAdvance' /> cng l mt la chn khng ti, n gip tng <span className='text-white'>tc  i/chy</span>, <span className='text-white'>st thng nhn c s tr vo mana</span>, tng <span className='text-white'>mu</span> v <span className='text-white'>khng la</span>.
                        </p>
                        <p>
                            Cn nu bn khng cn khng th <EquipmentUniqueName uniqueKey='WarTraveler' /> cng l mt la chn tuyt vi khi n c thm <span className='text-white'>t l nhn c vt phm ma thut</span>. Ngoi ra, ngi chi n S Druid Nguyn T La cng thng kim nhng <span className='text-rare font-weight-bold'>giy him</span> ph hp vi nhu cu hin ti ca nhn vt.
                        </p>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='SandstormTrek' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentSetPartFullInfo setKey='AldursWatchtower' setPartKey='AldursAdvance' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='WarTraveler' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}