import React, { useEffect } from 'react';

export interface RangeSliderData {
    from: number,
    to: number
}

function RangeSlider(input: {
    sliderKey: string,
    from: number, 
    to: number,
    min: number,
    max: number,
    onChange: (data: RangeSliderData) => void
}) {
    var fromValue = input.from;
    var toValue = input.to;

    const inputFromValueId = input.sliderKey + "_from";
    const inputToValueId = input.sliderKey + "_to";

    useEffect(() => {
        const script = document.createElement('script');
        script.innerHTML = `
        var setNativeValue = function (element, value) {
            const valueSetter = Object.getOwnPropertyDescriptor(element, 'value').set;
            const prototype = Object.getPrototypeOf(element);
            const prototypeValueSetter = Object.getOwnPropertyDescriptor(prototype, 'value').set;
            if (valueSetter && valueSetter !== prototypeValueSetter) {
                prototypeValueSetter.call(element, value);
            } else {
                valueSetter.call(element, value);
            }
        }
        var saveResult = function(data) {
            this.setNativeValue(document.getElementById('${inputFromValueId}'), data.from);
            document.getElementById('${inputFromValueId}').dispatchEvent(new Event('change', {
                'bubbles': true
            }));
            this.setNativeValue(document.getElementById('${inputToValueId}'), data.to);
            document.getElementById('${inputToValueId}').dispatchEvent(new Event('change', {
                'bubbles': true
            }));
        };
        $('#${input.sliderKey}').ionRangeSlider({
            type: 'double',
            grid: true,
            from: ${input.from},
            to: ${input.to},
            min: ${input.min},
            max: ${input.max},
            onFinish: function(data) {
                saveResult(data);
            }
        });
        `;
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        }
    }, [])
    return (
        <div>
            <input type="text" className="js-range-slider" id={input.sliderKey} />
            <input type="text" className='d-none' id={inputFromValueId} onChange={e => {
                const newFromValue = Number(e.target.value);
                if (newFromValue !== fromValue) {
                    fromValue = newFromValue;
                    input.onChange({from: fromValue, to: toValue})
                }
            }} />
            <input type="text" className='d-none' id={inputToValueId} onChange={e => {
                const newToValue: number = Number(e.target.value);
                if (newToValue !== toValue) {
                    toValue = newToValue;
                    input.onChange({from: fromValue, to: toValue})
                }
            }} />
        </div>
    );
}

export default RangeSlider;