import React from 'react';

import ModifierText from '../ModifierText';

import { EquipmentSetPartBase, EquipmentSetPartBaseProps } from '../../../models/Diablo2Models';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../services/Diablo2LocalizationService';
import { Diablo2EquipmentUniqueDataService, useDiablo2EquipmentUniqueDataService } from '../../../services/Diablo2EquipmentUniqueDataService';
import { Diablo2EquipmentBaseDataService, useDiablo2EquipmentBaseDataService } from '../../../services/Diablo2EquipmentBaseDataService';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();
const diablo2EquipmentUniqueDataService: Diablo2EquipmentUniqueDataService = useDiablo2EquipmentUniqueDataService();
const diablo2EquipmentBaseDataService: Diablo2EquipmentBaseDataService = useDiablo2EquipmentBaseDataService();

function UniqueBase(input: { partBase: EquipmentSetPartBase }) {
    var tier = ""
    if (input.partBase.tier) {
        tier = " - " + diablo2LocalizationService.getEquipmentTier(input.partBase.tier);
    }
    return (
        <div className="row text-white">
            <div className="col-12">
                Phi: <img
                    src={diablo2EquipmentBaseDataService.getEquipmentBaseImageUrl(input.partBase.image)}
                    alt={input.partBase.key}
                    width="20px"
                    style={{ maxWidth: "20px", maxHeight: "20px", objectFit: "cover", objectPosition: "top" }} /> {diablo2LocalizationService.getEquipmentName(input.partBase.nameKey)}{tier}
            </div>
        </div>
    );
}


function UniqueBaseWithoutImage(input: { partBase: EquipmentSetPartBase }) {
    var tier = ""
    if (input.partBase.tier) {
        tier = " - " + diablo2LocalizationService.getEquipmentTier(input.partBase.tier);
    }
    return (
        <div className="row text-white">
            <div className="col-12">
                Phi: {diablo2LocalizationService.getEquipmentName(input.partBase.nameKey)}{tier}
            </div>
        </div>
    );
}

function UniqueBaseProps(input: {
    baseProps: EquipmentSetPartBaseProps
}) {
    return (
        <div className='text-white'>
            <UniqueBaseProp title='Phng th' value={input.baseProps.defense} textColor="#84b7ff" />
            <UniqueBaseProp title='St thng mt tay' value={input.baseProps.oneHandDamage} textColor="#84b7ff" />
            <UniqueBaseProp title='St thng hai tay' value={input.baseProps.twoHandDamage} textColor="#84b7ff" />
            <UniqueBaseProp title='St thng mt tay (Ngi Man Di)' value={input.baseProps.oneHandDamageForBar} textColor="#84b7ff" />
            <UniqueBaseProp title='Tc  c bn' value={input.baseProps.baseSpeed} textColor="#84b7ff" />
            <UniqueBaseProp title=' bn' value={input.baseProps.durability} textColor="#84b7ff" />
            <UniqueBaseProp title='Yu cu sc mnh' value={input.baseProps.strengthRequired} textColor="#84b7ff" />
            <UniqueBaseProp title='Yu cu nhanh nhn' value={input.baseProps.dexterityRequired} textColor="#84b7ff" />
            <UniqueBaseProp title='Yu cu cp' value={input.baseProps.clvl} textColor="#84b7ff" />
            <UniqueBaseProp title='Cp cht lng' value={input.baseProps.qlvl} textColor="#84b7ff" />
            <UniqueBaseProp title='St thng ' value={input.baseProps.kickDamage} textColor="#84b7ff" />
            <UniqueBaseProp title='S  tht lng' value={input.baseProps.maxPotionSlot} textColor="#84b7ff" />
            <UniqueBaseProp title=' n' value={input.baseProps.block} textColor="#84b7ff" />
            {
                input.baseProps.classBlock
                    ? (
                        <div className="row">
                            <div className="col-sm-12">
                                 n theo lp nhn vt: <span style={{ color: "#84b7ff" }}>{input.baseProps.classBlock[0]}<br />
                                    {input.baseProps.classBlock[1]}<br />
                                    {input.baseProps.classBlock[2]}</span>
                            </div>
                        </div>
                    )
                    : (<></>)
            }
            <UniqueBaseProp title='St thng p' value={input.baseProps.smiteDamage} textColor="#84b7ff" />
            <div className="row">
                <div className="col-sm-12" style={{ color: "#ff7353" }}>
                    {input.baseProps.onlyClass
                        ? diablo2LocalizationService.getOnlyClassModifier(input.baseProps.onlyClass)
                        : ""}
                </div>
            </div>
        </div>
    )
}

function UniqueBaseProp(input: {
    title: string,
    value: string | number | undefined,
    textColor: string
}) {
    if (!input.value || input.value === "") {
        return (<></>);
    }

    return (
        <div className="row">
            <div className="col-sm-12">
                {input.title}: <span style={{ color: input.textColor }}>{input.value}</span>
            </div>
        </div>
    );
}

export default function EquipmentUniqueFullInfo(input: { uniqueKey: string }) {
    const validUnique = diablo2EquipmentUniqueDataService.getByKey(input.uniqueKey);

    return (
        <>
            <div className="row">
                <div className="col-12">
                    <div className='scale-part-img-container center-block'>
                        <img
                            src={diablo2EquipmentUniqueDataService.getEquipmentUniqueImageUrl(validUnique.image)}
                            alt={validUnique.key}
                            className='scale-part-img' />
                    </div>

                </div>
            </div>
            <div className="row">
                <div className="col-12" style={{ color: "#ecd2a8" }}>
                    {diablo2LocalizationService.getEquipmentName(validUnique.nameKey)}
                </div>
            </div>
            <div className="row">
                <div className="col-12" style={{ color: "#ecd2a8" }}>
                    {diablo2LocalizationService.getEquipmentTier(validUnique.tier)}
                </div>
            </div>
            {
                ["Charm", "Amulet", "Ring"].includes(validUnique.type)
                    ? <UniqueBaseWithoutImage partBase={validUnique.base} />
                    : <UniqueBase partBase={validUnique.base} />
            }
            <UniqueBaseProps baseProps={validUnique.baseProps} />
            <div style={{ color: "#84b7ff" }}>
                {
                    validUnique.modifiers.map((modifier, index) => {
                        const isLast = validUnique.modifiers.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={modifier} textOnly={false} /><br /></>
                            : <ModifierText modifier={modifier} textOnly={false} />;
                    })
                }
            </div>
        </>
    );
}