import React from 'react';

import RangeSlider from '../../../../../compoments/RangeSlider';

import { EquipmentUniqueFilterRequest } from '../../../../../services/Diablo2EquipmentUniqueDataService';

export default function DataEquipmentUniqueFilterByClvl(input: {
    filter: EquipmentUniqueFilterRequest,
    updateFilter: (filter: EquipmentUniqueFilterRequest) => void
}) {
    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    <label>Yu cu cp trong khong</label>
                    <RangeSlider sliderKey='level_filter_slider' from={input.filter.fromClvl} to={input.filter.toClvl} min={1} max={99} onChange={data => {
                        input.filter.fromClvl = data.from;
                        input.filter.toClvl = data.to;
                        input.updateFilter(input.filter);
                    }} />
                </div>
            </div>
        </div>
    );
}