import React from 'react';

import EquipmentBaseName from '../../../../../../compoments/diablo2/build/EquipmentBaseName';

import RunewordName from '../../../../../../compoments/diablo2/build/RunewordName';
import RunewordFullInfo from '../../../../../../compoments/diablo2/build/RunewordFullInfo';

export default function EquipmentShield() {
    return (
        <div id="equipment_shield_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_shield">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">Khin</h4>
                    </div>
                </a>
                <div id="collapse_shield" className="collapse skill-panel-content text-content" data-parent="#equipment_shield_accordion">
                    <div className="card-body">
                        C kh nhiu la chn  y, nhng la chn ti u nht vn l <EquipmentBaseName baseKey='Monarch' /> c 4 l v khm <RunewordName runewordKey='Spirit' />
                        . <RunewordName runewordKey='Splendor' /> cng l mt s thay th tuyt vi khi bn cha tm c phi ng  cho <RunewordName runewordKey='Spirit' />,
                        ngoi ra bn cng c th s dng <RunewordName runewordKey='Rhyme' />  tng <b><span className='text-white'> Khng </span></b> v <b><span className='text-white'> T L Nhn c Vt Phm Ma Thut</span></b>.
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Spirit' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Splendor' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Rhyme' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}