import React from 'react';

import { Rune } from '../../../../models/Diablo2Models';

import DataRuneTableHeader from "./DataRuneTableHeader";
import DataRuneTableRow from "./DataRuneTableRow";

export default function DataRuneTable(input: {
    runeData: Rune[],
}) {
    return (
        <div className="card-body table-responsive p-0 bg-content text-content table-d2">
            <table className="table">
                <DataRuneTableHeader />
                <tbody>
                    {
                        input.runeData.map((rune) => {
                            return (<DataRuneTableRow rune={rune} />)
                        })
                    }
                </tbody>
            </table>
        </div>
    )
}