import React from 'react';

import { RunewordFilterRequest } from '../../../../../services/Diablo2RunewordDataService';

export default function DataRunewordFilterSort(input: {
    filter: RunewordFilterRequest,
    updateFilter: (filter: RunewordFilterRequest) => void
}) {
    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    <label>Sp xp theo</label>
                    <div className="row">
                        <div className="col-sm-12">
                            <select className="form-control" style={{ fontFamily: "'Arial','FontAwesome'", backgroundColor: "#494038", color: "#FFFFFF" }} onChange={(event) => {
                                input.filter.sortBy = event.target.value;
                                input.updateFilter(input.filter);
                            }}>
                                <option value="name|asc">Tn: A &#xf178; Z</option>
                                <option value="name|desc">Tn: Z &#xf178; A</option>
                                <option value="clvl|asc">Cp: Thp n Cao</option>
                                <option value="clvl|desc">Cp: Cao n Thp</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}