import React from 'react';

import { Routes, Route, Navigate } from 'react-router-dom';

import './assets/diablo2/css/d2.css';
import './assets/diablo2/css/d2-icon.css';

import Header from './layouts/Header';
import Footer from './layouts/Footer';

import DataRune from './pages/diablo2/database/DataRune';
import DataRuneword from './pages/diablo2/database/DataRuneword';
import DataGem from './pages/diablo2/database/DataGem';
import DataRecipe from './pages/diablo2/database/DataRecipe';
import DataEquipmentBase from './pages/diablo2/database/DataEquipmentBase';
import DataEquipmentSet from './pages/diablo2/database/DataEquipmentSet';
import DataEquipmentUnique from './pages/diablo2/database/DataEquipmentUnique';
import DataEquipmentCraft from './pages/diablo2/database/DataEquipmentCraft';

import Summonmancer from './pages/diablo2/build/necromancer/Summonmancer';
import PoisonNova from './pages/diablo2/build/necromancer/PoisonNova';
import Blizzard from './pages/diablo2/build/sorceress/Blizzard';
import FistOfTheHeaven from './pages/diablo2/build/paladin/FistOfTheHeaven';

import PandemoniumEventPage from './pages/diablo2/guide/PandemoniumEventPage';
import Bonemancer from './pages/diablo2/build/necromancer/Bonemancer';
import FireElemental from './pages/diablo2/build/druid/FireElemental';
import Summoner from './pages/diablo2/build/druid/Summoner';

function App() {
    return (
        <div className="wrapper">
            <Header />
            <Routes>
                <Route path='/'>
                    <Route index element={<Navigate replace to='/diablo2/data/rune' />} />
                    <Route path='diablo2'>
                        <Route index element={<Navigate replace to='/diablo2/data/rune' />} />
                        <Route path='data'>
                            <Route index element={<Navigate replace to='/diablo2/data/rune' />} />
                            <Route path='rune' element={<DataRune />} />
                            <Route path='runeword' element={<DataRuneword />} />
                            <Route path='gem' element={<DataGem />} />
                            <Route path='recipe' element={<DataRecipe />} />
                            <Route path='equipment'>
                                <Route index element={<Navigate replace to='/diablo2/equipment/base' />} />
                                <Route path='base' element={<DataEquipmentBase />} />
                                <Route path='set' element={<DataEquipmentSet />} />
                                <Route path='unique' element={<DataEquipmentUnique />} />
                                <Route path='craft' element={<DataEquipmentCraft />} />
                            </Route>
                        </Route>
                        <Route path='build'>
                            <Route index element={<Navigate replace to='/diablo2/build/necromancer/poisonnova' />} />
                            <Route path='necromancer'>
                                <Route index element={<Navigate replace to='/diablo2/build/necromancer/poisonnova' />} />
                                <Route path='poisonnova' element={<PoisonNova />} />
                                <Route path='summonmancer' element={<Summonmancer />} />
                                <Route path='bonemancer' element={<Bonemancer />} />
                            </Route>
                            <Route path='druid'>
                                <Route index element={<Navigate replace to='/diablo2/build/druid/fireelemental' />} />
                                <Route path='fireelemental' element={<FireElemental />} />
                                <Route path='summoner' element={<Summoner />} />
                            </Route>
                            <Route path='paladin'>
                                <Route index element={<Navigate replace to='/diablo2/build/paladin/fistoftheheaven' />} />
                                <Route path='fistoftheheaven' element={<FistOfTheHeaven />} />
                            </Route>
                            <Route path='sorceress'>
                                <Route index element={<Navigate replace to='/diablo2/build/sorceress/blizzard' />} />
                                <Route path='blizzard' element={<Blizzard />} />
                            </Route>
                        </Route>
                        <Route path='guide'>
                            <Route index element={<Navigate replace to='/diablo2/guide/pandemonium' />} />
                            <Route path='pandemonium' element={<PandemoniumEventPage />} />
                        </Route>
                    </Route>
                </Route>
            </Routes>
            <Footer />
        </div>
    );
}

export default App;
