import React from 'react';

import ModifierText from '../../../compoments/diablo2/ModifierText';

import { ModifiersForBase } from '../../../models/Diablo2Models';

import { Diablo2RunewordDataService, useDiablo2RunewordDataService, RunewordFilterRequest } from '../../../services/Diablo2RunewordDataService';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../services/Diablo2LocalizationService';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();
const diablo2RunewordDataService: Diablo2RunewordDataService = useDiablo2RunewordDataService();

function RunewordModifiersForBaseSection(input: { modifiersForBase: ModifiersForBase, numberOfSlot: number }) {
    return (
        <>
            <p>
                Khm vo {
                    input.modifiersForBase.baseClasses.map((aBaseClass, index) => {
                        return index !== 0
                            ? <> / <b>{diablo2LocalizationService.getWeaponClassName(aBaseClass)}</b></>
                            : <><b>{diablo2LocalizationService.getWeaponClassName(aBaseClass)}</b></>;
                    })
                } c {input.numberOfSlot} l ngc s nhn c:
            </p>
            <p style={{ color: "#84b7ff" }}>
                {
                    input.modifiersForBase.modifiers.map((modifier, index) => {
                        const isLast = input.modifiersForBase.modifiers.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={modifier} textOnly={false} /><br /></>
                            : <ModifierText modifier={modifier} textOnly={false} />;
                    })
                }
            </p>
        </>
    )
}

export default function RunewordFullInfo(input: { runewordKey: string }) {
    const validRuneword = diablo2RunewordDataService.getByKey(input.runewordKey);

    return (
        <div className='text-center text-white'>
            <p style={{ color: "#ecd2a8", fontSize: "18px" }}>
                Ph Ng: {
                    diablo2LocalizationService.getRunewordName(validRuneword.nameKey)
                }
            </p>
            <p style={{ color: "#ffc107" }}>
                {
                    validRuneword.runes.map((rune, index) => {
                        const isLast = validRuneword.runes.length === (index + 1);
                        return !isLast
                            ? <> <span className={'d2-icon ' + ('d2-' + rune.toLowerCase())}></span>{rune} <i className="fa fa-caret-right text-secondary"></i></>
                            : <> <span className={'d2-icon ' + ('d2-' + rune.toLowerCase())}></span>{rune} </>;
                    })
                }
            </p>
            <p>Yu cu cp: {validRuneword.clvl}</p>
            <p>
                {
                    validRuneword.modifiersForBase.map((aModifiersForBase, index) => {
                        const isLast = validRuneword.modifiersForBase.length === (index + 1);
                        return !isLast
                            ? <><RunewordModifiersForBaseSection modifiersForBase={aModifiersForBase} numberOfSlot={validRuneword.runes.length} /><br /></>
                            : <><RunewordModifiersForBaseSection modifiersForBase={aModifiersForBase} numberOfSlot={validRuneword.runes.length} /></>
                    })
                }
            </p>
        </div>
    );
}