import React from 'react';

import { EquipmentUnique } from '../../../../../models/Diablo2Models';

import DataEquipmentUniqueListCard from './DataEquipmentUniqueListCard';


export default function DataEquipmentUniqueList(input: {
    data: EquipmentUnique[]
}) {
    return (
        <div className="col-lg-7">
            {
                input.data.map((uniqueEquipment) => {
                    return <DataEquipmentUniqueListCard uniqueEquipment={uniqueEquipment} />
                })
            }
        </div>
    );
}