import React from 'react';

import '../../../../../assets/diablo2/css/d2.css';
import './Blizzard.css'

import PageContentHeader from '../../../../../compoments/diablo2/PageContentHeader';

import Attribute from './Attribute';
import Skill from './Skill';
import Introduction from './Introduction';
import Equipment from './Equipment';
import Mercenary from './Mercenary';

function Blizzard() {
    return (
        <div className="content-wrapper bg-wrapper">
            <PageContentHeader headerText='Cch Build Php S Bo Bng' headerMapItems={["Cch Build", "Php S", "Bo Bng"]} />
            <div className="content">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="card">
                                <div className="card-body bg-content">
                                    <div className="row">
                                        <div className="col-5 col-sm-3">
                                            <div className="nav flex-column nav-tabs h-100 nav-build" id="vert-tabs-tab" role="tablist" aria-orientation="vertical">
                                                <a className="nav-link bg-content active" id="build-introduction-tab" data-toggle="pill" href="#build-introduction" role="tab" aria-controls="build-introduction" aria-selected="true">Gii thiu</a>
                                                <a className="nav-link" id="build-attribute-tab" data-toggle="pill" href="#build-attribute" role="tab" aria-controls="build-attribute" aria-selected="false">Tim nng</a>
                                                <a className="nav-link" id="build-skill-tab" data-toggle="pill" href="#build-skill" role="tab" aria-controls="build-skill" aria-selected="false">K nng</a>
                                                <a className="nav-link" id="build-equipment-tab" data-toggle="pill" href="#build-equipment" role="tab" aria-controls="build-equipment" aria-selected="false">Trang b cui</a>
                                                <a className="nav-link" id="build-merc-tab" data-toggle="pill" href="#build-merc" role="tab" aria-controls="build-merc" aria-selected="false">Lnh nh thu</a>
                                            </div>
                                        </div>
                                        <div className="col-7 col-sm-9">
                                            <div className="tab-content text-content" id="vert-tabs-tabContent">
                                                <Introduction />
                                                <Attribute />
                                                <Skill />
                                                <Equipment />
                                                <Mercenary />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}


export default Blizzard;