import React from 'react';

import RangeSlider from '../../../../../compoments/RangeSlider';

import { RunewordFilterRequest } from '../../../../../services/Diablo2RunewordDataService';

export default function DataRunewordFilterByCLvl(input: {
    filter: RunewordFilterRequest,
    updateFilter: (filter: RunewordFilterRequest) => void
}) {
    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    <label>Yu cu cp trong khong</label>
                    <RangeSlider sliderKey='level_filter_slider' from={input.filter.fromLevel} to={input.filter.toLevel} min={1} max={99} onChange={data => {
                        input.filter.fromLevel = data.from;
                        input.filter.toLevel = data.to;
                        input.updateFilter(input.filter);
                    }} />
                </div>
            </div>
        </div>
    );
}