import React from 'react';
import { useSearchParams } from "react-router-dom";

import '../../../../assets/diablo2/css/d2.css'
import './DataEquipmentBase.css';

import { Diablo2EquipmentBaseDataService, useDiablo2EquipmentBaseDataService, EquipmentBaseFilterRequest } from '../../../../services/Diablo2EquipmentBaseDataService';

import PageContentHeader from '../../../../compoments/diablo2/PageContentHeader';
import DataEquipmentBaseFilter from './DataEquipmentBaseFilter';
import DataEquipmentBaseList from './DataEquipmentBaseList';

const diablo2EquipmentBaseDataService: Diablo2EquipmentBaseDataService = useDiablo2EquipmentBaseDataService();

function DataEquipmentBase() {
    const [searchParams, setSearchParams] = useSearchParams();

    const numberSocketParam = searchParams.get("numberSocket");
    const numberSocket = numberSocketParam
        && Number(numberSocketParam)
        && Number(numberSocketParam) >= 2
        && Number(numberSocketParam) <= 6
        ? numberSocketParam
        : "all";
    const typeParam = trimToEmpty(searchParams.get("type"));
    const typeEquipment = typeParam !== "" ? typeParam : "Armor";

    const filterRequest: EquipmentBaseFilterRequest = {
        name: trimToEmpty(searchParams.get("name")),
        type: typeEquipment,
        numberSocket: numberSocket,
    };

    function updateFilter(filter: EquipmentBaseFilterRequest) {
        setSearchParams((params) => {
            if (!filter.name || filter.name.length === 0) {
                params.delete("name");
            } else {
                params.set("name", filter.name);
            }
            if (filter.type === "Armor") {
                params.delete("type");
            } else {
                params.set("type", filter.type);
            }
            if (filter.numberSocket === "all") {
                params.delete("numberSocket");
            } else {
                params.set("numberSocket", filter.numberSocket);
            }
            return params;
        })
    }

    function trimToEmpty(s: string | null | undefined) {
        return s ? s : "";
    }

    return (
        <div className="content-wrapper bg-wrapper">
            <PageContentHeader headerText="Trang B Bnh Thng (Phi/Trng)" headerMapItems={["D Liu", "Trang B", "Bnh Thng (Phi/Trng)"]} />
            <section className="content">
                <div className="container">
                    <DataEquipmentBaseFilter filter={filterRequest} updateFilter={updateFilter} />
                    <DataEquipmentBaseList filteredEquipmentBases={diablo2EquipmentBaseDataService.getAllByFilter(filterRequest)} />
                </div>
            </section>
        </div>
    );
}

export default DataEquipmentBase;