import React, { useEffect } from 'react';
import { useSearchParams } from "react-router-dom";

import '../../../../assets/diablo2/css/d2.css'
import '../../../../assets/diablo2/css/d2-icon.css'
import './DataRuneword.css';

import PageContentHeader from '../../../../compoments/diablo2/PageContentHeader';

import { Diablo2RunewordDataService, useDiablo2RunewordDataService, RunewordFilterRequest } from '../../../../services/Diablo2RunewordDataService';

import DataRunewordFilter from './DataRunewordFilter';
import DataRunewordList from './DataRunewordList/DataRunewordList';

const diablo2RunewordDataService: Diablo2RunewordDataService = useDiablo2RunewordDataService();

export default function DataRuneword() {
    useEffect(() => {
        const script = document.createElement('script');
        script.innerHTML = `
        $(document).ready(function(){
            $('[data-toggle="tooltip"]').tooltip();
          });
        `;
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        }
    }, [])

    const [searchParams, setSearchParams] = useSearchParams();

    const numberSlotParam = searchParams.get("numberSlot");
    const numberSlot = numberSlotParam
        && Number(numberSlotParam)
        && Number(numberSlotParam) >= 2
        && Number(numberSlotParam) <= 6
        ? numberSlotParam
        : "all";
    const fromLevelParam = Number(searchParams.get("fromLevel"));
    const fromLevel = fromLevelParam
        && fromLevelParam >= 1
        && fromLevelParam <= 99
        ? fromLevelParam
        : 1;
    const toLevelParam = Number(searchParams.get("toLevel"));
    const toLevel = toLevelParam
        && toLevelParam >= 1
        && toLevelParam <= 99
        && toLevelParam >= fromLevelParam
        ? toLevelParam
        : 99;
    const equipmentTypesParam = trimToEmpty(searchParams.get("equipmentTypes")).trim();
    const equipmentTypes = equipmentTypesParam ? equipmentTypesParam.split("|") : [];
    const sortByParam = trimToEmpty(searchParams.get("sortBy"));
    const sortBy = ["name|asc", "name|desc", "clvl|asc", "clvl|desc"].includes(sortByParam)
        ? sortByParam
        : "name|asc";

    const filterRequest: RunewordFilterRequest = {
        keyword: trimToEmpty(searchParams.get("keyword")),
        numberSlot: numberSlot,
        fromLevel: fromLevel,
        toLevel: toLevel,
        equipmentTypes: equipmentTypes,
        sortBy: sortBy,
    };

    function updateFilter(filter: RunewordFilterRequest) {
        setSearchParams((params) => {
            if (!filter.keyword || filter.keyword.length === 0) {
                params.delete("keyword");
            } else {
                params.set("keyword", filter.keyword);
            }
            if (filter.numberSlot === "all") {
                params.delete("numberSlot");
            } else {
                params.set("numberSlot", filter.numberSlot);
            }
            if (filter.fromLevel === 1) {
                params.delete("fromLevel");
            } else {
                params.set("fromLevel", filter.fromLevel.toString());
            }
            if (filter.toLevel === 99) {
                params.delete("toLevel");
            } else {
                params.set("toLevel", filter.toLevel.toString());
            }
            if (!filter.equipmentTypes || filter.equipmentTypes.length === 0) {
                params.delete("equipmentTypes");
            } else {
                params.set("equipmentTypes", filter.equipmentTypes.join("|"));
            }
            if (filter.sortBy === "name|asc") {
                params.delete("sortBy");
            } else {
                params.set("sortBy", filter.sortBy);
            }
            return params;
        })
    }

    function trimToEmpty(s: string | null | undefined) {
        return s ? s : "";
    }

    return (
        <div className="content-wrapper bg-wrapper">
            <PageContentHeader headerText="Ph Ng" headerMapItems={["D Liu", "Ph Ng"]} />
            <div className="content">
                <div className="container">
                    <div className="row">
                        <DataRunewordFilter filter={filterRequest} updateFilter={updateFilter} />
                        <DataRunewordList data={diablo2RunewordDataService.getByFilter(filterRequest)} />
                    </div>
                </div>
            </div>
        </div>
    );
}