import React from 'react';

import ModifierText from '../../../../../../../compoments/diablo2/ModifierText';

import { EquipmentSetPart } from '../../../../../../../models/Diablo2Models';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../../../../services/Diablo2LocalizationService';
import { Diablo2EquipmentSetDataService, useDiablo2EquipmentSetDataService } from '../../../../../../../services/Diablo2EquipmentSetDataService';

import EquipmentSetListPartItemSetBonuses from './EquipmentSetListPartItemSetBonuses';
import EquipmentSetListPartItemBaseProps from './EquipmentSetListPartItemBaseProps';
import EquipmentSetListPartItemBase from './EquipmentSetListPartItemBase';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();
const diablo2EquipmentSetDataService: Diablo2EquipmentSetDataService = useDiablo2EquipmentSetDataService();

export default function EquipmentSetListPartItem(input: { part: EquipmentSetPart, allParts: EquipmentSetPart[] }) {
    return (
        <>
            <div className="row">
                <div className="col-12">
                    <div className='scale-part-img-container center-block'>
                        <img
                            src={diablo2EquipmentSetDataService.getEquipmentSetImageUrl(input.part.image)}
                            alt={input.part.key}
                            className='scale-part-img' />
                    </div>

                </div>
            </div>

            <div className="row">
                <div className="col-12 text-set">
                    {diablo2LocalizationService.getEquipmentName(input.part.nameKey)}
                </div>
            </div>

            <EquipmentSetListPartItemBase partBase={input.part.base} />
            <EquipmentSetListPartItemBaseProps baseProps={input.part.baseProps} />

            <div style={{ color: "#84b7ff" }}>
                {
                    input.part.modifiers.map((modifier, index) => {
                        const isLast = input.part.modifiers.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={modifier} textOnly={false} /><br /></>
                            : <ModifierText modifier={modifier} textOnly={false} />;
                    })
                }
            </div>

            {
                input.part.setBonus
                    ? <EquipmentSetListPartItemSetBonuses partSetBonuses={input.part.setBonus} parts={input.allParts} />
                    : ""
            }
        </>
    );
}