import React from 'react';

import ModifierText from '../../../../../../../compoments/diablo2/ModifierText';

import { EquipmentSetBonus } from '../../../../../../../models/Diablo2Models';

export default function EquipmentSetListBonusItem(input: { setBonus: EquipmentSetBonus }) {
    return (
        <>
            <p>C {input.setBonus.numberOfPart.replaceAll("all", "tt c")} mn trang b:</p>
            <p className='set-item-props'>
                {
                    input.setBonus.modifiers.map((modifier, index) => {
                        const isLast = input.setBonus.modifiers.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={modifier} textOnly={false} /><br /></>
                            : <ModifierText modifier={modifier} textOnly={false} />;
                    })
                }
                {
                    input.setBonus.extraNote && input.setBonus.extraNote.length > 0
                        ? <><br /> {input.setBonus.extraNote[0]}</>
                        : <></>
                }
            </p>
        </>
    );
}