import React from 'react';

import { Gem } from '../../../../models/Diablo2Models';
import { Diablo2GemDataService, useDiablo2GemDataService } from '../../../../services/Diablo2GemDataService';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../services/Diablo2LocalizationService';

import ModifierText from '../../../../compoments/diablo2/ModifierText';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();
const diablo2GemDataService: Diablo2GemDataService = useDiablo2GemDataService();

export default function DataGemTableRow(input: { gem: Gem }) {
    return (
        <tr>
            <td><img src={diablo2GemDataService.getGemImageUrl(input.gem)} alt={input.gem.key} className='gem-img' /></td>
            <td>{diablo2LocalizationService.getGemName(input.gem.nameKey)}</td>
            <td>
                {
                    input.gem.armorBonus.map((armorBonus, index) => {
                        const isLast = input.gem.armorBonus.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={armorBonus} textOnly /><br /></>
                            : <ModifierText modifier={armorBonus} textOnly />;
                    })
                }
            </td>
            <td>
                {
                    input.gem.shieldBonus.map((shieldBonus, index) => {
                        const isLast = input.gem.shieldBonus.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={shieldBonus} textOnly /><br /></>
                            : <ModifierText modifier={shieldBonus} textOnly />;
                    })
                }
            </td>
            <td>
                {
                    input.gem.weaponBonus.map((weaponBonus, index) => {
                        const isLast = input.gem.weaponBonus.length === (index + 1);
                        return !isLast
                            ? <> <ModifierText modifier={weaponBonus} textOnly /><br /></>
                            : <ModifierText modifier={weaponBonus} textOnly />;
                    })
                }
            </td>
        </tr>
    );
}