import _equipmentUnique from '../assets/diablo2/database/equipment-unique.json';
import { EquipmentUnique } from '../models/Diablo2Models'
import { Diablo2LocalizationService, useDiablo2LocalizationService } from './Diablo2LocalizationService';

const equipmentUniqueData = _equipmentUnique as EquipmentUnique[];
const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export type Diablo2EquipmentUniqueDataService = ReturnType<typeof useDiablo2EquipmentUniqueDataService>;

export interface EquipmentUniqueFilterRequest {
    keyword: string;
    fromClvl: number;
    toClvl: number;
    equipmentTier: string[];
    equipmentClass: string;
    sortBy: string;
}

export const useDiablo2EquipmentUniqueDataService = () => {

    function getAll() {
        return equipmentUniqueData;
    }

    function getByFilter(filterRequest: EquipmentUniqueFilterRequest) {

        // filter by key
        if (filterRequest.keyword.startsWith("#")) {
            const uniqueEquipment = getByKey(filterRequest.keyword.replace("#", ""));
            return uniqueEquipment ? [uniqueEquipment] : [];
        }

        return equipmentUniqueData.filter((aEquipment) => {
            var isMatch = true;

            // by name
            if (filterRequest.keyword) {
                isMatch = isMatch && diablo2LocalizationService.getEquipmentName(aEquipment.nameKey)
                    .toLowerCase()
                    .includes(filterRequest.keyword.toLowerCase());
            }

            // by required level
            if (aEquipment.baseProps.clvl && Number.isInteger(parseInt(aEquipment.baseProps.clvl))) {
                if (filterRequest.fromClvl) {
                    isMatch = isMatch && parseInt(aEquipment.baseProps.clvl) >= filterRequest.fromClvl;
                }

                if (filterRequest.toClvl) {
                    isMatch = isMatch && parseInt(aEquipment.baseProps.clvl) <= filterRequest.toClvl;
                }
            }

            // by equipment tier
            if (aEquipment.tier) {
                if (filterRequest.equipmentTier && filterRequest.equipmentTier.length > 0) {
                    isMatch = isMatch && filterRequest.equipmentTier.includes(aEquipment.tier);
                }
            }

            // by equipment class
            if (aEquipment.type) {
                if (filterRequest.equipmentClass && filterRequest.equipmentClass.length > 0) {
                    isMatch = isMatch && aEquipment.type.startsWith(filterRequest.equipmentClass)
                }
            }

            return isMatch;
        }).sort((aEquipment, otherEquipment) => {
            if (filterRequest.sortBy.startsWith("name|")) {
                return filterRequest.sortBy.endsWith("|asc")
                    ? diablo2LocalizationService.getEquipmentName(aEquipment.nameKey)
                        .localeCompare(diablo2LocalizationService.getEquipmentName(otherEquipment.nameKey))
                    : diablo2LocalizationService.getEquipmentName(aEquipment.nameKey)
                        .localeCompare(diablo2LocalizationService.getEquipmentName(otherEquipment.nameKey))
            }

            if (filterRequest.sortBy.startsWith("clvl|")) {
                const clvl = aEquipment.baseProps.clvl && Number.isInteger(parseInt(aEquipment.baseProps.clvl))
                    ? parseInt(aEquipment.baseProps.clvl)
                    : 0
                const otherClvl = otherEquipment.baseProps.clvl && Number.isInteger(parseInt(otherEquipment.baseProps.clvl))
                    ? parseInt(otherEquipment.baseProps.clvl)
                    : 0
                return filterRequest.sortBy.endsWith("|asc")
                    ? clvl - otherClvl
                    : otherClvl - clvl
            }

            const qlvl = aEquipment.baseProps.qlvl && Number.isInteger(parseInt(aEquipment.baseProps.qlvl))
                ? parseInt(aEquipment.baseProps.qlvl)
                : 0
            const otherQlvl = otherEquipment.baseProps.qlvl && Number.isInteger(parseInt(otherEquipment.baseProps.qlvl))
                ? parseInt(otherEquipment.baseProps.qlvl)
                : 0
            return filterRequest.sortBy.endsWith("|asc")
                ? qlvl - otherQlvl
                : otherQlvl - qlvl
        })
    }

    function getByKey(uniqueKey: string) {
        return equipmentUniqueData.filter((aUnique) => {
            return aUnique.key === uniqueKey;
        })[0];
    }

    function getEquipmentUniqueImageUrl(imageUrl: string) {
        return "/images/diablo2/equipment/unique/" + imageUrl;
    }

    return {
        getAll,
        getByFilter,
        getByKey,
        getEquipmentUniqueImageUrl,
    }
}