import React from 'react';

import { Gem } from '../../../../models/Diablo2Models';

import DataGemTableHeader from './DataGemTableHeader';
import DataGemTableRow from './DataGemTableRow';

export default function DataGemTable(input: {
    gems: Gem[]
}) {
    return (
        <div className="card-body table-responsive p-0 bg-content table-d2">
            <table className="table">
                <DataGemTableHeader />
                <tbody>
                    {input.gems.map((gem) => {
                        return (<DataGemTableRow gem={gem} />)
                    })}
                </tbody>
            </table>
        </div>
    )
}