import React from 'react';

import { Diablo2SkillDataService, useDiablo2SkillDataService } from '../../../services/Diablo2SkillDataService';

const diablo2SkillDataService: Diablo2SkillDataService = useDiablo2SkillDataService();

export default function SkillMinLevel(input: {skillKey: string}) {
    const minclvl = diablo2SkillDataService.getByKey(input.skillKey).minclvl;

    return (
        <>{minclvl ? minclvl : "ang cp nht"}</>
    );
}