import React, { useEffect } from 'react';

import { EquipmentSet } from '../../../../../../models/Diablo2Models';

import { Diablo2EquipmentSetDataService, useDiablo2EquipmentSetDataService } from '../../../../../../services/Diablo2EquipmentSetDataService';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../../../services/Diablo2LocalizationService';

import DataEquipmentSetDetail from './DataEquipmentSetDetail';

const diablo2EquipmentSetDataService: Diablo2EquipmentSetDataService = useDiablo2EquipmentSetDataService();
const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export default function DataEquipmentSetListItem(input: { equipmentSet: EquipmentSet }) {
    var keyButton = input.equipmentSet.key + "Button";
    var keyModel = input.equipmentSet.key;

    useEffect(() => {
        const script = document.createElement('script');
        script.innerHTML = `
        var elemm = document.getElementById('${keyButton}'); 
        elemm.onclick = function() { $('#${keyModel}').modal('show'); };
        `;
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        }
    }, [])

    return (
        <>
            <div className="info-box list-item bg-content" id={keyButton}>
                <span className="info-box-icon list-item-icon">
                    <img src={diablo2EquipmentSetDataService.getEquipmentSetImageUrl(input.equipmentSet.image)} />
                </span>
                <div className="info-box-content">
                    <span className="info-box-text list-item-name">
                        {diablo2LocalizationService.getEquipmentName(input.equipmentSet.nameKey)}
                    </span>
                    <span className="info-box-text list-item-info">
                        {diablo2LocalizationService.getEquipmentTier(input.equipmentSet.tier)} - {input.equipmentSet.parts && input.equipmentSet.parts.length > 0 ? <>{input.equipmentSet.parts.length} vt phm</> : <>ang cp nht</>}
                    </span>
                </div>
            </div>
            <DataEquipmentSetDetail equipmentSet={input.equipmentSet} />
        </>
    );
}