import React, { ReactNode } from 'react';
import reactStringReplace from 'react-string-replace';

import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../services/Diablo2LocalizationService';
import { Modifier } from '../../models/Diablo2Models';
import { Diablo2SkillDataService, useDiablo2SkillDataService } from '../../services/Diablo2SkillDataService';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();
const diablo2SkillDataService: Diablo2SkillDataService = useDiablo2SkillDataService();

const basedOnLevelModifierKeys = ["ModStr1cBasedOnLevel", "ModStr5qBasedOnLevel", "ModStr1xBasedOnLevel", "ModStr1uBasedOnLevel",
    "ModStr1wBasedOnLevel", "ModStr4eBasedOnLevel", "ModStr1aBasedOnLevel", "ModStr1iBasedOnLevel", "ModStr1eBasedOnLevel",
    "ModStr3vBasedOnLevel", "ModStre9pBasedOnLevel", "ModStr1fBasedOnLevel", "ModStr1sBasedOnLevel", "ModStr1hBasedOnLevel",
    "strModLightningDamageBasedOnLevel"];

function transformModifierWithTypeParameter(modifier: Modifier, textOnly: boolean) {
    var s: string | ReactNode[] = "";
    const modifierText = diablo2LocalizationService.getModifier(modifier.key);
    if (modifierText) {
        s = modifierText;
        if (modifier.params) {
            var count = 0;
            s = reactStringReplace(s, /(%[+]?[ds][%]{0,2})/g, (match, i) => (
                transformParam(modifier.params[count++], match)
            ));
        }
    }
    return (<>{s}</>);
}

function transformModifierWithIndexParameter(modifier: Modifier) {
    var s: string | ReactNode[] = "";
    const modifierText = diablo2LocalizationService.getModifier(modifier.key);
    if (modifierText) {
        s = modifierText;
        if (modifier.params) {
            modifier.params.forEach((param, index) => {
                if (s.includes("%" + index + "%%")) {
                    s = reactStringReplace(s, "%" + index + "%%", (match, i) => (
                        transformParam(param, "%" + index + "%%")
                    ));
                } else {
                    s = reactStringReplace(s, "%" + index, (match, i) => (
                        transformParam(param, "%" + index)
                    ));
                }

            })
        }
    }
    return (<>{s}</>);
}

function transformGroupModifier(modifier: Modifier) {
    var s: string | ReactNode[] = "";
    const modifierText = diablo2LocalizationService.getModifier(modifier.key);
    if (modifierText) {
        s = modifierText.replaceAll("%s", "").trim() + " %s";
        if (modifier.params) {
            s = reactStringReplace(s, "\n", (match) => (
                <br />
            ));
            var count = 0;
            s = reactStringReplace(s, /(%[+]?[ds][%]{0,2})/g, (match, i) => (
                transformParam(modifier.params[count++], match)
            ));
        }
    }
    return (
        <>
            {s}<br />
            {
                modifier.extraModifiers.map((extraModifier) => {
                    return <><ModifierText modifier={extraModifier} textOnly={false} /><br /></>
                })
            }
        </>);
}

function transformBasedOnLevelModifier(modifier: Modifier) {
    var s: string | ReactNode[] = "";
    const modifierText = diablo2LocalizationService.getModifier(modifier.key);
    if (modifierText) {
        s = modifierText;
        if (modifier.params) {
            var count = 0;
            s = reactStringReplace(s, /(%[+]?[ds][%]{0,2})/g, (match, i) => {
                if (count !== 0) {
                    return "";
                }
                count++;

                var prefix = "";
                var postfix = "";

                if (match.includes("+")) {
                    if (modifier.params[1] && modifier.params[1].startsWith("range:-")) {
                        prefix = "-";
                    } else {
                        prefix = "+";
                    }
                }

                if (match.endsWith("%")) {
                    postfix = "%";
                }

                var tooltipText = "";
                if (modifier.params[1]) {
                    tooltipText = tooltipText + "X c gi tr: " + modifier.params[1].replaceAll("range:", "");
                    if (modifier.params[2]) {
                        tooltipText = tooltipText + "<br/>";
                        tooltipText = tooltipText + "Mi cp tng: " + modifier.params[2].replaceAll("perLevel:", "");
                    }
                } else {
                    tooltipText = "Khng c thng tin"
                }


                return (
                    <>
                        {prefix}<span className='modifier-param-range' data-toggle="tooltip" data-placement="top" data-html="true" title={tooltipText}>
                            {modifier.params[0]}
                        </span>{postfix}
                    </>

                );
            });
        }
    }
    return (<>{s}</>);
}

function isGroupModifier(modifierKey: string) {
    return modifierKey === "SkillItemMetamorphosisWolf" || modifierKey === "SkillItemMetamorphosisBear";
}

function isIndexParameter(modifierString: string) {
    return modifierString.includes("%0");
}

function transformParam(param: string, parameterName: string) {
    var result;
    var prefix = "";
    var postfix = "";

    // check param prefix
    if (parameterName.includes("+")) {
        if (param.startsWith("range:") && !param.startsWith("range:-")) {
            prefix = "+"
        }
        if (Number(param) && Number(param) >= 0) {
            prefix = "+"
        }
        if (param === "X") {
            prefix = "+"
        }
    }

    // check param postfix
    if (parameterName.endsWith("%")) {
        postfix = "%";
    }

    // transform param
    if (param.startsWith("range:")) {
        if (param.startsWith("range:-")) {
            result = <span className='modifier-param-range'>{param.replaceAll("range:-", "")}</span>;
            prefix = "-"
        } else {
            result = <span className='modifier-param-range'>{param.replaceAll("range:", "")}</span>;
        }
    } else if (param.startsWith("skill:")) {
        result = diablo2SkillDataService.getSkillNameByKey(param.replaceAll("skill:", ""));
    } else if (param.startsWith("monster:")) {
        result = diablo2LocalizationService.getMonsterName(param.replaceAll("monster:", ""));
    } else if (param.startsWith("onlyClass:")) {
        result = diablo2LocalizationService.getOnlyClassModifier(param.replaceAll("onlyClass:", ""));
    } else if (param.startsWith("time-second:")) {
        result = param.replace("time-second:", "") + " giy";
    } else {
        result = param;
    }
    return (<span>{prefix}{result}{postfix}</span>);
}

export default function ModifierText(input: {
    modifier: Modifier,
    textOnly: boolean,
}) {
    if (basedOnLevelModifierKeys.includes(input.modifier.key)) {
        return transformBasedOnLevelModifier(input.modifier);
    }

    if (isGroupModifier(input.modifier.key)) {
        return transformGroupModifier(input.modifier);
    }

    // enrich params for skill charges
    if (input.modifier.key === "ModStre10d" && input.modifier.params.length === 3) {
        input.modifier.params.push(input.modifier.params[2]);
    }

    const modifierLocalization = diablo2LocalizationService.getModifier(input.modifier.key);

    if (isIndexParameter(modifierLocalization)) {
        return transformModifierWithIndexParameter(input.modifier);
    }
    return transformModifierWithTypeParameter(input.modifier, input.textOnly);
}
