import React from 'react';

import { RunewordFilterRequest } from '../../../../../services/Diablo2RunewordDataService';

export default function DataRunewordFilterByName(input: {
    filter: RunewordFilterRequest,
    updateFilter: (filter: RunewordFilterRequest) => void
}) {
    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    <label>Tn</label>
                    <input className="form-control" type="text" placeholder="Ph ng, kha,..." value={input.filter.keyword} onChange={event => {
                        input.filter.keyword = event.target.value;
                        input.updateFilter(input.filter);
                    }} />
                </div>
            </div>
        </div>
    )
}