import React, { useEffect } from 'react';

import '../../../../assets/diablo2/css/d2.css'
import './DataEquipmentSet.css';

import PageContentHeader from '../../../../compoments/diablo2/PageContentHeader';

import { Diablo2EquipmentSetDataService, useDiablo2EquipmentSetDataService } from '../../../../services/Diablo2EquipmentSetDataService';

import DataEquipmentSetList from './DataEquipmentSetList';

const diablo2EquipmentSetDataService: Diablo2EquipmentSetDataService = useDiablo2EquipmentSetDataService();

export default function DataEquipmentSet() {
    useEffect(() => {
        const script = document.createElement('script');
        script.innerHTML = `
        $(document).ready(function(){
            $('[data-toggle="tooltip"]').tooltip();
          });
        `;
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        }
    }, [])

    return (
        <div className="content-wrapper bg-wrapper">
            <section className="content-header">
                <div className="container">
                    <div className="row mb-2">
                        <div className="col-sm-6 text-set">
                            <h1>Trang B Theo B (Xanh L)</h1>
                        </div>
                        <div className="col-sm-6">
                            <ol className="breadcrumb float-sm-right">
                                <li className="breadcrumb-item"><a href="#">D Liu</a></li>
                                <li className="breadcrumb-item"><a href="#">Trang B</a></li>
                                <li className="breadcrumb-item active">Theo B (Xanh L)</li>
                            </ol>
                        </div>
                    </div>
                </div>
            </section>

            <section className="content">
                <div className="container text-white">
                    <h4>Bnh thng</h4>
                    <DataEquipmentSetList equipmentSets={diablo2EquipmentSetDataService.getAllByTier("Normal")} />
                    <h4>Xut sc</h4>
                    <DataEquipmentSetList equipmentSets={diablo2EquipmentSetDataService.getAllByTier("Exceptional")} />
                    <h4>Tinh ty</h4>
                    <DataEquipmentSetList equipmentSets={diablo2EquipmentSetDataService.getAllByTier("Elite")} />
                </div>
            </section>
        </div>
    );
}