import React from 'react';

import '../../../../assets/diablo2/css/d2.css'
import './DataRune.css';

import PageContentHeader from '../../../../compoments/diablo2/PageContentHeader';

import { Diablo2RuneDataService, useDiablo2RuneDataService } from '../../../../services/Diablo2RuneDataService';

import DataRuneTable from './DataRuneTable';

const diablo2RuneDataService: Diablo2RuneDataService = useDiablo2RuneDataService();

export default function DataRune() {
    return (
        <div className="content-wrapper bg-wrapper">
            <PageContentHeader headerText='Ph Vn' headerMapItems={["D Liu", "Ph Vn"]} />
            <section className="content">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="card">
                                <DataRuneTable runeData={diablo2RuneDataService.getAll()} />
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}