import React from 'react';

import { RunewordFilterRequest } from '../../../../../services/Diablo2RunewordDataService';

export default function DataRunewordFilterByEquipmentClass(input: {
    filter: RunewordFilterRequest,
    updateFilter: (filter: RunewordFilterRequest) => void
}) {
    function updateEquipmentClassFilter(equipmentClass: string) {
        if (input.filter.equipmentTypes.includes(equipmentClass)) {
            input.filter.equipmentTypes = input.filter.equipmentTypes.filter(e => e !== equipmentClass);
        } else {
            input.filter.equipmentTypes.push(equipmentClass);
        }
        input.updateFilter(input.filter);
    }

    function isActiveEquipmentClass(equipmentClass: string) {
        return input.filter.equipmentTypes.includes(equipmentClass) ? " active" : "";
    }

    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    <label>C th khm vo</label>

                    <div className="row">
                        <div className="col-sm-12" style={{ marginTop: "-5px" }}>
                            <button
                                type="button"
                                className={"btn btn-secondary" + (isActiveEquipmentClass("Body Armor"))}
                                style={{ marginRight: "5px", marginTop: "5px" }}
                                onClick={(event) => { updateEquipmentClassFilter("Body Armor") }}>
                                <span className='d2-icon d2-armor'></span>o Gip
                            </button>
                            <button
                                type="button"
                                className={"btn btn-secondary" + (isActiveEquipmentClass("Helm"))}
                                style={{ marginRight: "5px", marginTop: "5px" }}
                                onClick={(event) => { updateEquipmentClassFilter("Helm") }}>
                                <span className='d2-icon d2-helm'></span>M
                            </button>
                            <button
                                type="button"
                                className={"btn btn-secondary" + (isActiveEquipmentClass("Shield"))}
                                style={{ marginRight: "5px", marginTop: "5px" }}
                                onClick={(event) => { updateEquipmentClassFilter("Shield") }}>
                                <span className='d2-icon d2-shield'></span>Khin
                            </button>
                            <button
                                type="button"
                                className={"btn btn-secondary" + (isActiveEquipmentClass("Paladin Shield"))}
                                style={{ marginTop: "5px" }}
                                onClick={(event) => { updateEquipmentClassFilter("Paladin Shield") }}>
                                <span className='d2-icon d2-shield-paladin'></span>Khin Hip S Thnh
                            </button>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col-sm-12" style={{ marginTop: "10px" }}>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Weapon"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Weapon");
                            }}><span className='d2-icon d2-flail'></span>V Kh</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Melee Weapon"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Melee Weapon");
                            }}><span className='d2-icon d2-warscythe'></span>V Kh Cn Chin</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Missile Weapon"))} style={{ marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Missile Weapon");
                            }}><span className='d2-icon d2-crossbow'></span>V Kh Tm Xa</button>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col-sm-12" style={{ marginTop: "10px" }}>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Polearm"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Polearm");
                            }}><span className='d2-icon d2-polearm'></span>Trng Binh</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Spear"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Spear");
                            }}><span className='d2-icon d2-spear'></span>Thng</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Katar"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Katar");
                            }}><span className='d2-icon d2-claw'></span>Vut</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Axe"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Axe");
                            }}><span className='d2-icon d2-axe'></span>Ru</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Sword"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Sword");
                            }}><span className='d2-icon d2-sword'></span>Kim</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Hammer"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Hammer");
                            }}><span className='d2-icon d2-hammer'></span>Ba</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Club"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Club");
                            }}><span className='d2-icon d2-club'></span>Chy G</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Scepter"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Scepter");
                            }}><span className='d2-icon d2-scepter'></span>Quyn Trng</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Dagger"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Dagger");
                            }}><span className='d2-icon d2-dagger'></span>Dao Gm</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Mace"))} style={{ marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Mace");
                            }}><span className='d2-icon d2-mace'></span>Chy</button>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col-sm-12" style={{ marginTop: "10px" }}>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Wand"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Wand");
                            }}><span className='d2-icon d2-wand'></span>Gy Php</button>
                            <button type="button" className={"btn btn-secondary" + (isActiveEquipmentClass("Staff"))} style={{ marginTop: "5px" }} onClick={(event) => {
                                updateEquipmentClassFilter("Staff");
                            }}><span className='d2-icon d2-staff'></span>Trng</button>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}