import React from 'react';

import RunewordName from '../../../../../../compoments/diablo2/build/RunewordName';

import SkillName from '../../../../../../compoments/diablo2/build/SkillName';

export default function MercenarySkill() {
    return (
        <div id="equipment_mer_skill_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_mer_skill">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">K Nng</h4>
                    </div>
                </a>
                <div id="collapse_mer_skill" className="collapse skill-panel-content text-content" data-parent="#equipment_mer_skill_accordion">
                    <div className="card-body">
                        Bn c th chn lnh nh thu c k nng ho quang <SkillName skillKey='Might' />  tng st thng cho m lnh triu hi ca mnh. Hoc bn cng c th s dng lnh nh thu c k nng ho quang <SkillName skillKey='HolyFreeze' />  tng kh nng sng st cho mnh v m lnh triu hi, nhng hy lu , k nng <SkillName skillKey='HolyFreeze' /> s khin m qui vt b ng bng v bn s khng th s dng xc ca qui vt no cht khi b ng bng  triu hi hoc s dng <SkillName skillKey='CorpseExplosion' />.
                    </div>
                </div>
            </div>
        </div>
    )
}