import React from 'react';

export default function DataEquipmentBaseListCardPropBlockForOneClass(input: {
    class: string,
    normalValue: string | undefined,
    exceptionalValue: string | undefined,
    eliteValue: string | undefined,
    textColor: string
}) {
    if ((!input.normalValue || input.normalValue === "")
        && (!input.exceptionalValue || input.exceptionalValue === "")
        && (!input.eliteValue || input.eliteValue === "")) {
        return (<></>);
    }

    return (
        <div className="row" style={{ color: input.textColor }}>
            <div className="col-5" style={{ textAlign: "left", paddingLeft: "5%" }}>
                T l  n
            </div>
            <div className="col-2">
                {!input.normalValue || input.normalValue === ""
                    ? "-"
                    : input.class + ": " + input.normalValue}
            </div>
            <div className="col-2">
                {!input.exceptionalValue || input.exceptionalValue === ""
                    ? "-"
                    : input.class + ": " + input.exceptionalValue}
            </div>
            <div className="col-2">
                {!input.eliteValue || input.eliteValue === ""
                    ? "-"
                    : input.class + ": " + input.eliteValue}
            </div>
        </div>
    );
}