import React from 'react';

import { EquipmentBase } from '../../../../models/Diablo2Models'

import DataEquipmentBaseListCardForCirclet from './DataEquipmentBaseListCardForCirclet';
import DataEquipmentBaseListCard from './DataEquipmentBaseListCard';

export default function DataEquipmentBaseList(input: { filteredEquipmentBases: EquipmentBase[] }) {
    return (
        <div className="row">
            <div className="col-12">
                {input.filteredEquipmentBases.map((aEquipmentBase) => {
                    if (aEquipmentBase.normal.key === 'Circlet') {
                        return (<DataEquipmentBaseListCardForCirclet equipmentBase={aEquipmentBase} />)
                    }
                    return (<DataEquipmentBaseListCard equipmentBase={aEquipmentBase} />)
                })}
            </div>
        </div>
    );
}