import _skillData from '../assets/diablo2/database/skill.json';

import { Skill } from '../models/Diablo2Models'

import { Diablo2LocalizationService, useDiablo2LocalizationService } from './Diablo2LocalizationService';

const skillData = _skillData as Skill[];
const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export type Diablo2SkillDataService = ReturnType<typeof useDiablo2SkillDataService>;

export const useDiablo2SkillDataService = () => {
    
    function getByKey(skillKey: string) {
        return skillData.filter((aSkill) => {
            return aSkill.key === skillKey;
        })[0];
    }

    function getSkillNameByKey(skillKey: string) {
        const skill = getByKey(skillKey);
        if (skill.key === "Delirium") {
            return diablo2LocalizationService.getRunewordName(skill.nameKey);
        }
        return diablo2LocalizationService.getSkillName(skill.nameKey);
    }

    function getSkillImageUrl(imageUrl: string) {
        return "/images/diablo2/skill/" + imageUrl;
    }

    return {
        getByKey,
        getSkillNameByKey,
        getSkillImageUrl,
    }
}