import React from 'react';

import RunewordName from '../../../../../../compoments/diablo2/build/RunewordName';
import RunewordFullInfo from '../../../../../../compoments/diablo2/build/RunewordFullInfo';

export default function EquipmentArmor() {
    return (
        <div id="equipment_bodyArmor_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_bodyArmor">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">o Gip</h4>
                    </div>
                </a>
                <div id="collapse_bodyArmor" className="collapse skill-panel-content text-content" data-parent="#equipment_bodyArmor_accordion">
                    <div className="card-body">
                        <p>
                            Ging nh nhng cch build khc, <RunewordName runewordKey='Enigma' /> cng l la chn tt nht dnh cho n S Druid Nguyn T La. Tuy nhin,  ch to c ph ng ny th kh l kh khn, n cn c 2 ph vn l <span className='text-rune'>Jah</span> v <span className='text-rune'>Ber</span>, 2 ph vn kh tm nht trong tr chi. C mt la chn hi km mt cht m bn c th s dng khi cha c <RunewordName runewordKey='Enigma' /> l <RunewordName runewordKey='Rain' />, n s tng <span className='text-white'>+2 cp k nng ca n S Druid</span> v cng thm <span className='text-white'>kha kh mana</span>.
                        </p>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Enigma' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Rain' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}