import _equipmentBase from '../assets/diablo2/database/equipment-base.json';
import { EquipmentBase, EquipmentBaseProps } from '../models/Diablo2Models'
import { Diablo2LocalizationService, useDiablo2LocalizationService } from './Diablo2LocalizationService';


const equipmentBaseData = _equipmentBase as EquipmentBase[];
const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export interface EquipmentBaseFilterRequest {
    name: string,
    type: string,
    numberSocket: string;
}

export type Diablo2EquipmentBaseDataService = ReturnType<typeof useDiablo2EquipmentBaseDataService>;

export const useDiablo2EquipmentBaseDataService = () => {

    function getByKey(baseKey: string) : EquipmentBaseProps | undefined {
        var validBaseProps;
        for (var aBase of equipmentBaseData) {
            if (aBase.normal && aBase.normal.key === baseKey) {
                return aBase.normal;
            }
            if (aBase.normal2 && aBase.normal2.key === baseKey) {
                return aBase.normal2;
            } 
            if (aBase.exceptional && aBase.exceptional.key === baseKey) {
                return aBase.exceptional;
            } 
            if (aBase.elite && aBase.elite.key === baseKey) {
                return aBase.elite;
            } 
        }
        return validBaseProps;
    }

    function getAll() {
        return equipmentBaseData;
    }

    function getAllByFilter(requestFilter: EquipmentBaseFilterRequest) {
        return equipmentBaseData.filter((aEquipmentBase) => {
            var isMatch = true;

            // filter name
            if (requestFilter.name) {
                isMatch = isMatch
                    && (
                        diablo2LocalizationService.getEquipmentName(aEquipmentBase.normal.nameKey).toLowerCase().includes(requestFilter.name.toLowerCase())
                        || diablo2LocalizationService.getEquipmentName(aEquipmentBase.exceptional.nameKey).toLowerCase().includes(requestFilter.name.toLowerCase())
                        || diablo2LocalizationService.getEquipmentName(aEquipmentBase.elite.nameKey).toLowerCase().includes(requestFilter.name.toLowerCase())
                    )
                if (aEquipmentBase.normal2) {
                    isMatch = isMatch || diablo2LocalizationService.getEquipmentName(aEquipmentBase.normal2.nameKey).toLowerCase().includes(requestFilter.name.toLowerCase())
                }
            }

            // filter type
            if (requestFilter.type.length > 0) {
                var filteringTypes = [requestFilter.type];
                if (requestFilter.type === "Weapon") {
                    filteringTypes = [
                        "Weapon-Polearm",
                        "Weapon-Spear",
                        "Weapon-Claw",
                        "Weapon-Axe",
                        "Weapon-Sword",
                        "Weapon-Hammer",
                        "Weapon-Club",
                        "Weapon-Scepter",
                        "Weapon-Dagger",
                        "Weapon-Mace",
                        "Weapon-Bow",
                        "Weapon-Crossbow",
                        "Weapon-Javelin",
                        "Weapon-Throwing",
                        "Weapon-Orb",
                        "Weapon-Wand",
                        "Weapon-Staff"];
                }
                if (requestFilter.type === "Weapon-Melee") {
                    filteringTypes = [
                        "Weapon-Polearm",
                        "Weapon-Spear",
                        "Weapon-Claw",
                        "Weapon-Axe",
                        "Weapon-Sword",
                        "Weapon-Hammer",
                        "Weapon-Club",
                        "Weapon-Scepter",
                        "Weapon-Dagger",
                        "Weapon-Mace",
                        "Weapon-Orb",
                        "Weapon-Wand",
                        "Weapon-Staff"];
                }
                if (requestFilter.type === "Weapon-Range") {
                    filteringTypes = [
                        "Weapon-Bow",
                        "Weapon-Crossbow",
                        "Weapon-Javelin",
                        "Weapon-Throwing"];
                }

                isMatch = isMatch && filteringTypes.map((filteringType) => {
                    return aEquipmentBase.normal.type.startsWith(filteringType);
                }).includes(true);
            }

            // filter by maxsocket
            if (requestFilter.numberSocket && Number(requestFilter.numberSocket)) {
                isMatch = isMatch
                    && (
                        aEquipmentBase.normal.maxSocket === Number(requestFilter.numberSocket)
                        || aEquipmentBase.exceptional.maxSocket === Number(requestFilter.numberSocket)
                        || aEquipmentBase.elite.maxSocket === Number(requestFilter.numberSocket)
                    );
            }

            return isMatch;
        });
    }

    function getEquipmentBaseImageUrl(image: string) {
        return "/images/diablo2/equipment/base/" + image;
    }

    return {
        getByKey,
        getAll,
        getAllByFilter,
        getEquipmentBaseImageUrl,
    }
}