import React from 'react';

import EquipmentBaseName from '../../../../../../compoments/diablo2/build/EquipmentBaseName';

import RunewordName from '../../../../../../compoments/diablo2/build/RunewordName';
import RunewordFullInfo from '../../../../../../compoments/diablo2/build/RunewordFullInfo';

export default function MercenaryArmor() {
    return (
        <div id="equipment_mer_armor_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_mer_armor">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">o Gip</h4>
                    </div>
                </a>
                <div id="collapse_mer_armor" className="collapse skill-panel-content text-content" data-parent="#equipment_mer_armor_accordion">
                    <div className="card-body">
                        <RunewordName runewordKey='Fortitude' /> hoc <RunewordName runewordKey='Duress' /> s dng phi o gip v hnh c phng th cao. <RunewordName runewordKey='Treachery' /> cng l mt la chn khng ti.
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Fortitude' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Duress' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Treachery' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}