import React from 'react';

import { Diablo2SkillDataService, useDiablo2SkillDataService } from '../../../services/Diablo2SkillDataService';

const diablo2SkillDataService: Diablo2SkillDataService = useDiablo2SkillDataService();

export default function SkillName(input: {skillKey: string}) {
    
    return (
        <span className='text-white'>{diablo2SkillDataService.getSkillNameByKey(input.skillKey)}</span>
    );
}