import React from 'react';

import { EquipmentUniqueFilterRequest } from '../../../../../services/Diablo2EquipmentUniqueDataService';

export default function DataEquipmentUniqueFilterSort(input: {
    filter: EquipmentUniqueFilterRequest,
    updateFilter: (filter: EquipmentUniqueFilterRequest) => void
}) {
    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    <label>Sp xp theo</label>
                    <div className="row">
                        <div className="col-sm-12">
                            <select className="form-control" style={{ fontFamily: "'Arial','FontAwesome'", backgroundColor: "#494038", color: "#FFFFFF"}} onChange={(event) => {
                                input.filter.sortBy = event.target.value;
                                input.updateFilter(input.filter);
                            }}>
                                <option value="name|asc">Tn: A &#xf178; Z</option>
                                <option value="name|desc">Tn: Z &#xf178; A</option>
                                <option value="clvl|asc">Cp yu cu: Thp n Cao</option>
                                <option value="clvl|desc">Cp yu cu: Cao n Thp</option>
                                <option value="qlvl|asc">Cp cht lng: Thp n Cao</option>
                                <option value="qlvl|desc">Cp cht lng: Cao n Thp</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}