import React from 'react';

import { Runeword } from '../../../../../models/Diablo2Models';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../../services/Diablo2LocalizationService';
import DataRunewordListCardBaseModifiers from './DataRunewordListCardBaseModifiers';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export default function DataRunewordListCard(input: { runeword: Runeword }) {
    return (
        <div className="card text-content bg-content text-center">
            <div className="card-body">
                <p className="card-text text-runeword-key">#{input.runeword.key}</p>
                <p className="card-text text-unique" style={{fontSize: "18px" }}>
                    {
                        diablo2LocalizationService.getRunewordName(input.runeword.nameKey)
                    }
                </p>
                <p className="text-rune">
                    {
                        input.runeword.runes.map((rune, index) => {
                            const isLast = input.runeword.runes.length === (index + 1);
                            return !isLast
                                ? <> <span className={'d2-icon ' + ('d2-' + rune.toLowerCase())}></span>{rune} <i className="fa fa-caret-right text-secondary"></i></>
                                : <> <span className={'d2-icon ' + ('d2-' + rune.toLowerCase())}></span>{rune} </>;
                        })
                    }
                </p>
                <p className="text-white">Yu cu cp: {input.runeword.clvl}</p>
                <p>
                    {
                        input.runeword.modifiersForBase.map((aModifiersForBase, index) => {
                            const isLast = input.runeword.modifiersForBase.length === (index + 1);
                            return !isLast
                                ? <><DataRunewordListCardBaseModifiers modifiersForBase={aModifiersForBase} numberOfSlot={input.runeword.runes.length} /><br /></>
                                : <><DataRunewordListCardBaseModifiers modifiersForBase={aModifiersForBase} numberOfSlot={input.runeword.runes.length} /></>
                        })
                    }
                </p>
            </div>
        </div>
    )
}