import React, { Component, ReactNode, useEffect, useState } from 'react';
import { useSearchParams } from "react-router-dom";

import '../../../../assets/diablo2/css/d2.css'
import '../../../../assets/diablo2/css/d2-icon.css'
import './DataEquipmentUnique.css';

import {
    Diablo2EquipmentUniqueDataService,
    useDiablo2EquipmentUniqueDataService,
    EquipmentUniqueFilterRequest
} from '../../../../services/Diablo2EquipmentUniqueDataService';
import DataEquipmentUniqueFilter from './DataEquipmentUniqueFilter';
import DataEquipmentUniqueList from './DataEquipmentUniqueList';

const diablo2EquipmentUniqueDataService: Diablo2EquipmentUniqueDataService = useDiablo2EquipmentUniqueDataService();

function DataEquipmentUnique() {
    useEffect(() => {
        const script = document.createElement('script');
        script.innerHTML = `
        $(document).ready(function(){
            $('[data-toggle="tooltip"]').tooltip();
          });
        `;
        document.body.appendChild(script);
        return () => {
            document.body.removeChild(script);
        }
    }, [])

    const [searchParams, setSearchParams] = useSearchParams();

    const fromClvlParam = Number(searchParams.get("fromClvl"));
    const fromClvl = fromClvlParam
        && fromClvlParam >= 1
        && fromClvlParam <= 99
        ? fromClvlParam
        : 1;
    const toClvlParam = Number(searchParams.get("toClvl"));
    const toClvl = toClvlParam
        && toClvlParam >= 1
        && toClvlParam <= 99
        && toClvlParam >= toClvlParam
        ? toClvlParam
        : 99;
    const equipmentTierParam = trimToEmpty(searchParams.get("equipmentTier")).trim();
    const equipmentTier = equipmentTierParam ? equipmentTierParam.split("|") : [];
    const equipmentClassParam = trimToEmpty(searchParams.get("equipmentClass"));
    const equipmentClass = equipmentClassParam.length > 0
        ? equipmentClassParam
        : "Armor";
    const sortByParam = trimToEmpty(searchParams.get("sortBy"));
    const sortBy = ["name|asc", "name|desc", "clvl|asc", "clvl|desc", "qlvl|asc", "qlvl|desc"].includes(sortByParam)
        ? sortByParam
        : "name|asc";

    const filterRequest: EquipmentUniqueFilterRequest = {
        keyword: trimToEmpty(searchParams.get("keyword")),
        fromClvl: fromClvl,
        toClvl: toClvl,
        equipmentTier: equipmentTier,
        equipmentClass: equipmentClass,
        sortBy: sortBy,
    };

    function updateFilter(filter: EquipmentUniqueFilterRequest) {
        setSearchParams((params) => {
            if (!filter.keyword || filter.keyword.length === 0) {
                params.delete("keyword");
            } else {
                params.set("keyword", filter.keyword);
            }

            if (filter.fromClvl === 1) {
                params.delete("fromClvl");
            } else {
                params.set("fromClvl", filter.fromClvl.toString());
            }
            if (filter.toClvl === 99) {
                params.delete("toClvl");
            } else {
                params.set("toClvl", filter.toClvl.toString());
            }

            if (!filter.equipmentTier || filter.equipmentTier.length === 0) {
                params.delete("equipmentTier");
            } else {
                params.set("equipmentTier", filter.equipmentTier.join("|"));
            }

            if (!filter.equipmentClass
                || filter.equipmentClass.length === 0
                || filter.equipmentClass === "Armor") {
                params.delete("equipmentClass");
            } else {
                params.set("equipmentClass", filter.equipmentClass);
            }

            if (filter.sortBy === "name|asc") {
                params.delete("sortBy");
            } else {
                params.set("sortBy", filter.sortBy);
            }
            return params;
        })
    }

    function trimToEmpty(s: string | null | undefined) {
        return s ? s : "";
    }

    return (
        <div className="content-wrapper bg-wrapper">
            <section className="content-header">
                <div className="container">
                    <div className="row mb-2">
                        <div className="col-sm-6 text-unique">
                            <h1>Trang B c Nht (Nu)</h1>
                        </div>
                        <div className="col-sm-6">
                            <ol className="breadcrumb float-sm-right">
                                <li className="breadcrumb-item"><a href="#">D Liu</a></li>
                                <li className="breadcrumb-item"><a href="#">Trang B</a></li>
                                <li className="breadcrumb-item active">c Nht (Nu)</li>
                            </ol>
                        </div>
                    </div>
                </div>
            </section>

            <div className="content">
                <div className="container">
                    <div className="row">
                        <DataEquipmentUniqueFilter filter={filterRequest} updateFilter={updateFilter} />
                        <DataEquipmentUniqueList data={diablo2EquipmentUniqueDataService.getByFilter(filterRequest)} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default DataEquipmentUnique;