import React from 'react';

import { RunewordFilterRequest } from '../../../../../services/Diablo2RunewordDataService';

import DataRunewordFilterByName from './DataRunewordFilterByName';
import DataRunewordFilterBySocket from './DataRunewordFilterBySocket';
import DataRunewordFilterByCLvl from './DataRunewordFilterByCLvl';
import DataRunewordFilterByEquipmentClass from './DataRunewordFilterByEquipmentClass';
import DataRunewordFilterSort from './DataRunewordFilterSort';

export default function DataRunewordFilter(input: {
    filter: RunewordFilterRequest,
    updateFilter: (filter: RunewordFilterRequest) => void
}) {
    return (
        <div className="col-lg-5">
            <div className="card card-runeword-filter">
                <div className="card-header">
                    <h3 className="card-title">B lc</h3>
                </div>

                <div className="card-body bg-content text-content">
                    <form>
                        <DataRunewordFilterByName filter={input.filter} updateFilter={input.updateFilter} />
                        <DataRunewordFilterBySocket filter={input.filter} updateFilter={input.updateFilter} />
                        <DataRunewordFilterByCLvl filter={input.filter} updateFilter={input.updateFilter} />
                        <DataRunewordFilterByEquipmentClass filter={input.filter} updateFilter={input.updateFilter} />
                        <DataRunewordFilterSort filter={input.filter} updateFilter={input.updateFilter} />
                    </form>
                </div>
            </div>
        </div>
    );
}