import _gemData from '../assets/diablo2/database/gem.json';
import { Gem } from '../models/Diablo2Models'

const gemData = _gemData as Gem[];

export type Diablo2GemDataService = ReturnType<typeof useDiablo2GemDataService>;

export const useDiablo2GemDataService = () => {

    function getAll() {
        return gemData;
    }

    function getByKey(gemKey: string) {
        return gemData.filter((aGem) => {
            return aGem.key === gemKey;
        })[0];
    }

    function getGemImageUrl(gem: Gem) {
        return "/images/diablo2/gem/" + gem.image;
    }

    return {
        getAll,
        getByKey,
        getGemImageUrl,
    }
}
