import React from 'react';

export default function DataEquipmentBaseListCardPropKeepZero(input: {
    title: string,
    normalValue: string | number | undefined,
    exceptionalValue: string | number | undefined,
    eliteValue: string | number | undefined,
    textColor: string
}) {
    if (input.normalValue === undefined
        && input.exceptionalValue === undefined
        && input.eliteValue === undefined) {
        return (<></>);
    }

    return (
        <div className="row" style={{ color: input.textColor }}>
            <div className="col-5" style={{ textAlign: "left", paddingLeft: "5%" }}>
                {input.title}
            </div>
            <div className="col-2">
                {input.normalValue}
            </div>
            <div className="col-2">
                {input.exceptionalValue}
            </div>
            <div className="col-2">
                {input.eliteValue}
            </div>
        </div>
    );
}