import React from 'react';

import { EquipmentSetPart } from '../../../../../../../models/Diablo2Models';
import EquipmentSetListPartItem from './EquipmentSetListPartItem';

export default function EquipmentSetListPart(input: { parts: EquipmentSetPart[] }) {
    const chunkSize = 2;
    const rows = [];
    for (let i = 0; i < input.parts.length; i += chunkSize) {
        const chunk = input.parts.slice(i, i + chunkSize);

        if (chunk.length === 2) {
            rows.push((
                <div className="row" style={{ marginBottom: "40px", borderBottom: "0.5px solid white", paddingBottom: "25px" }}>
                    <div className="col-sm-6 text-center">
                        <EquipmentSetListPartItem part={chunk[0]} allParts={input.parts} />
                    </div>
                    <div className="col-sm-6 text-center border-left">
                        <EquipmentSetListPartItem part={chunk[1]} allParts={input.parts} />
                    </div>
                </div>
            ));
        }

        if (chunk.length === 1) {
            rows.push((
                <div className="row">
                    <div className="col-sm-12 text-center">
                        <EquipmentSetListPartItem part={chunk[0]} allParts={input.parts} />
                    </div>
                </div>
            ));
        }
    };

    return (
        <>{rows}</>
    );
}