import React from 'react';

import EquipmentUniqueName from '../../../../../../compoments/diablo2/build/EquipmentUniqueName';
import EquipmentUniqueFullInfo from '../../../../../../compoments/diablo2/build/EquipmentUniqueFullInfo';

export default function EquipmentRing() {
    return (
        <div id="equipment_ring_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_ring">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">Nhn</h4>
                    </div>
                </a>
                <div id="collapse_ring" className="collapse skill-panel-content text-content" data-parent="#equipment_ring_accordion">
                    <div className="card-body">
                        <p>
                            Tng t vi dy chuyn, la chn tt nht dnh cho bn l <EquipmentUniqueName uniqueKey='StoneofJordan' />. Tuy nhin chic nhn ny kh him, trc khi tm c n, bn c th s dng nhn tng <span className='text-white'>tt c khng</span>, <span className='text-white'>mu</span> v <span className='text-white'>tc  ra chiu</span>.
                        </p>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='StoneofJordan' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}