import React from 'react';

import EquipmentBaseName from '../../../../../../compoments/diablo2/build/EquipmentBaseName';

import RunewordName from '../../../../../../compoments/diablo2/build/RunewordName';
import RunewordFullInfo from '../../../../../../compoments/diablo2/build/RunewordFullInfo';

import SkillName from '../../../../../../compoments/diablo2/build/SkillName';

export default function MercenaryWeapon() {
    return (
        <div id="equipment_mer_weapon_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_mer_weapon">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">V Kh</h4>
                    </div>
                </a>
                <div id="collapse_mer_weapon" className="collapse skill-panel-content text-content" data-parent="#equipment_mer_weapon_accordion">
                    <div className="card-body">
                        <p>
                            <RunewordName runewordKey='Infinity' />  c c k nng ho quang <SkillName skillKey='Conviction' /> gip bn gim phng th v khng la ca k ch. Bn cng c th s dng <RunewordName runewordKey='Obedience' /> hoc <RunewordName runewordKey='Insight' />
                        </p>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Infinity' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Obedience' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Insight' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}