import React from 'react';

import { Diablo2SkillDataService, useDiablo2SkillDataService } from '../../../services/Diablo2SkillDataService';

const diablo2SkillDataService: Diablo2SkillDataService = useDiablo2SkillDataService();

export default function SkillImage(input: {skillKey: string}) {
    const image = diablo2SkillDataService.getByKey(input.skillKey).image;
    
    return (
        <>{image ? (<img src={diablo2SkillDataService.getSkillImageUrl(image)} alt={input.skillKey} />) : "ang cp nht"}</>
    );
}