import React from 'react';

import { EquipmentSet } from '../../../../../../../models/Diablo2Models';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../../../../services/Diablo2LocalizationService';

import EquipmentSetListBonus from './EquipmentSetListBonus';
import EquipmentSetListPart from './EquipmentSetListPart';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export default function DataEquipmentSetDetail(input: { equipmentSet: EquipmentSet }) {
    return (
        <div className="modal fade bd-example-modal-xl" tabIndex={-1} role="dialog" aria-labelledby="myExtraLargeModalLabel" aria-hidden="true" id={input.equipmentSet.key}>
            <div className="modal-dialog modal-xl" role="document" >
                <div className="modal-content bg-content">
                    <div className="modal-header">
                        <h5 className="modal-title text-set" id="exampleModalLabel">{diablo2LocalizationService.getEquipmentName(input.equipmentSet.nameKey)}</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true"></span>
                        </button>
                    </div>
                    <div className="modal-body">
                        {
                            input.equipmentSet.setBonus
                                ? <><EquipmentSetListBonus setBonuses={input.equipmentSet.setBonus} /></>
                                : <>ang cp nht</>
                        }
                    </div>
                    <div className="modal-body border-top">
                        {
                            input.equipmentSet.parts
                                ? <><EquipmentSetListPart parts={input.equipmentSet.parts} /></>
                                : <>ang cp nht</>
                        }
                    </div>
                </div>
            </div>
        </div>
    );
}