import React from 'react';

import ModifierText from '../../../../../compoments/diablo2/ModifierText';
import { ModifiersForBase } from '../../../../../models/Diablo2Models';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../../services/Diablo2LocalizationService';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export default function DataRunewordListCardBaseModifiers(input: { modifiersForBase: ModifiersForBase, numberOfSlot: number }) {
    return (
        <div>
            <p className="text-white">
                Khm vo {
                    input.modifiersForBase.baseClasses.map((aBaseClass, index) => {
                        return index !== 0
                            ? <> / <b>{diablo2LocalizationService.getWeaponClassName(aBaseClass)}</b></>
                            : <><b>{diablo2LocalizationService.getWeaponClassName(aBaseClass)}</b></>;
                    })
                } c {input.numberOfSlot} l ngc s nhn c:
            </p>
            <p className="text-magic">
                {
                    input.modifiersForBase.modifiers.map((modifier, index) => {
                        const isLast = input.modifiersForBase.modifiers.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={modifier} textOnly={false} /><br /></>
                            : <ModifierText modifier={modifier} textOnly={false} />;
                    })
                }
            </p>
        </div>
    )
}