import React from 'react';

import { RunewordFilterRequest } from '../../../../../services/Diablo2RunewordDataService';

export default function DataRunewordFilterBySocket(input: {
    filter: RunewordFilterRequest,
    updateFilter: (filter: RunewordFilterRequest) => void
}) {
    return (
        <div className="row">
            <div className="col-sm-12">
                {/* select */}
                <div className="form-group">
                    <label>S l ngc yu cu</label>
                    <select className="custom-select" value={input.filter.numberSlot} onChange={(event) => {
                        input.filter.numberSlot = event.target.value;
                        input.updateFilter(input.filter);
                    }}>
                        <option value="all">Tt c</option>
                        <option value="2">2 l ngc</option>
                        <option value="3">3 l ngc</option>
                        <option value="4">4 l ngc</option>
                        <option value="5">5 l ngc</option>
                        <option value="6">6 l ngc</option>
                    </select>
                </div>
            </div>
        </div>
    )
}