import React from 'react';

import RunewordName from '../../../../../../compoments/diablo2/build/RunewordName';
import RunewordFullInfo from '../../../../../../compoments/diablo2/build/RunewordFullInfo';
import EquipmentUniqueName from '../../../../../../compoments/diablo2/build/EquipmentUniqueName';
import SkillName from '../../../../../../compoments/diablo2/build/SkillName';
import EquipmentUniqueFullInfo from '../../../../../../compoments/diablo2/build/EquipmentUniqueFullInfo';

export default function EquipmentShield() {
    return (
        <div id="equipment_shield_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_shield">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">Khin</h4>
                    </div>
                </a>
                <div id="collapse_shield" className="collapse skill-panel-content text-content" data-parent="#equipment_shield_accordion">
                    <div className="card-body">
                        <p>
                            <RunewordName runewordKey='Spirit' /> l mn trang b huyn thoi m mi php s u cn. Ngoi <RunewordName runewordKey='Spirit' />, bn cng c th s dng <EquipmentUniqueName uniqueKey='Homunculus' />, n s gip bn tng <span className='text-white'>k nng</span>, <span className='text-white'>phng th</span> v <span className='text-white'>khng</span>.
                        </p>
                        <p>
                            n y th nhiu ngi s ngh l sao khng i ng ging v kh, chng ta c th s dng <RunewordName runewordKey='Faith' />  tng cng cho k nng ho quang <SkillName skillKey='Fanaticism' />, nhng thc t nu s dng <RunewordName runewordKey='Faith' /> th bn s mt mt lng kha kh k nng, v iu  l khng ng.
                        </p>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <RunewordFullInfo runewordKey='Spirit' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='Homunculus' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}