import React from 'react';

import { EquipmentSet } from "../../../../../models/Diablo2Models";
import DataEquipmentSetListItem from './DataEquipmentSetListItem';

export default function DataEquipmentSetList(input: {equipmentSets: EquipmentSet[]}) {
    const chunkSize = 3;
    const setRows = [];
    for (let i = 0; i < input.equipmentSets.length; i += chunkSize) {
        const chunk = input.equipmentSets.slice(i, i + chunkSize);
        setRows.push((
            <div className="row">
                <div className="col-sm-4">
                    {chunk.length >= 1 ? <DataEquipmentSetListItem equipmentSet={chunk[0]} /> : <></>}
                </div>
                <div className="col-sm-4">
                    {chunk.length >= 2 ? <DataEquipmentSetListItem equipmentSet={chunk[1]} /> : <></>}
                </div>
                <div className="col-sm-4">
                    {chunk.length >= 3 ? <DataEquipmentSetListItem equipmentSet={chunk[2]} /> : <></>}
                </div>
            </div>
        ));
    };

    return (
        <>{setRows}</>
    )
}