import React from 'react';

import { EquipmentSetPartBaseProps } from '../../../../../../../models/Diablo2Models';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../../../../services/Diablo2LocalizationService';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export default function EquipmentSetListPartItemBaseProps(input: {
    baseProps: EquipmentSetPartBaseProps
}) {
    return (
        <>
            <EquipmentSetListPartItemBaseProp title='Phng th' value={input.baseProps.defense} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='St thng mt tay' value={input.baseProps.oneHandDamage} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='St thng hai tay' value={input.baseProps.twoHandDamage} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='St thng mt tay (Ngi Man Di)' value={input.baseProps.oneHandDamageForBar} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='Tc  c bn' value={input.baseProps.baseSpeed} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title=' bn' value={input.baseProps.durability} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='Yu cu sc mnh' value={input.baseProps.strengthRequired} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='Yu cu nhanh nhn' value={input.baseProps.dexterityRequired} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='Yu cu cp' value={input.baseProps.clvl} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='Cp cht lng' value={input.baseProps.qlvl} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='St thng ' value={input.baseProps.kickDamage} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title='S  tht lng' value={input.baseProps.maxPotionSlot} textColor="#84b7ff" />
            <EquipmentSetListPartItemBaseProp title=' n' value={input.baseProps.block} textColor="#84b7ff" />
            {
                input.baseProps.classBlock
                    ? (
                        <div className="row">
                            <div className="col-sm-12">
                                 n theo lp nhn vt: <span style={{ color: "#84b7ff" }}>{input.baseProps.classBlock[0]}<br />
                                    {input.baseProps.classBlock[1]}<br />
                                    {input.baseProps.classBlock[2]}</span>
                            </div>
                        </div>
                    )
                    : (<></>)
            }
            <EquipmentSetListPartItemBaseProp title='St thng p' value={input.baseProps.smiteDamage} textColor="#84b7ff" />
            <div className="row">
                <div className="col-sm-12" style={{ color: "#ff7353" }}>
                    {input.baseProps.onlyClass
                        ? diablo2LocalizationService.getOnlyClassModifier(input.baseProps.onlyClass)
                        : ""}
                </div>
            </div>

        </>
    )
}

function EquipmentSetListPartItemBaseProp(input: {
    title: string,
    value: string | number | undefined,
    textColor: string
}) {
    if (!input.value || input.value === "") {
        return (<></>);
    }

    return (
        <div className="row">
            <div className="col-sm-12">
                {input.title}: <span style={{ color: input.textColor }}>{input.value}</span>
            </div>
        </div>
    );
}