import React from 'react';

import ModifierText from '../../../../compoments/diablo2/ModifierText';
import CupeRepiceIngredientText from '../../../../compoments/diablo2/CupeRepiceIngredientText';

import { Diablo2RuneDataService, useDiablo2RuneDataService } from '../../../../services/Diablo2RuneDataService';
import { Rune } from '../../../../models/Diablo2Models';

const diablo2RuneDataService: Diablo2RuneDataService = useDiablo2RuneDataService();

export default function DataRuneTableRow(input: {
    rune: Rune
}) {
    return (
        <tr>
            <td><img src={diablo2RuneDataService.getRuneImageUrl(input.rune)} alt={input.rune.key} className='rune-img' /></td>
            <td>{input.rune.no}</td>
            <td>{input.rune.name}</td>
            <td>{input.rune.clvl}</td>
            <td>
                {
                    input.rune.armorBonus.map((armorBonus, index) => {
                        const isLast = input.rune.armorBonus.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={armorBonus} textOnly /> <br /></>
                            : <ModifierText modifier={armorBonus} textOnly />;
                    })
                }
            </td>
            <td>
                {
                    input.rune.shieldBonus.map((shieldBonus, index) => {
                        const isLast = input.rune.shieldBonus.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={shieldBonus} textOnly /> <br /></>
                            : <ModifierText modifier={shieldBonus} textOnly />;
                    })
                }
            </td>
            <td>
                {
                    input.rune.weaponBonus.map((weaponBonus, index) => {
                        const isLast = input.rune.weaponBonus.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={weaponBonus} textOnly /> <br /></>
                            : <ModifierText modifier={weaponBonus} textOnly />;
                    })
                }
            </td>
            <td>
                {
                    !input.rune.cubeRecipe ? "-" : input.rune.cubeRecipe.map((cupeRepiceIngredient, index) => {
                        const isLast = input.rune.cubeRecipe.length === (index + 1);
                        return !isLast
                            ? <><CupeRepiceIngredientText cupeRepiceIngredient={cupeRepiceIngredient} /><br /></>
                            : <CupeRepiceIngredientText cupeRepiceIngredient={cupeRepiceIngredient} />;
                    })
                }
            </td>
        </tr>
    );
}