import React from 'react';

import { EquipmentUniqueFilterRequest } from '../../../../../services/Diablo2EquipmentUniqueDataService';

export default function DataEquipmentUniqueFilterByTier(input: {
    filter: EquipmentUniqueFilterRequest,
    updateFilter: (filter: EquipmentUniqueFilterRequest) => void
}) {
    function updateFilter(equipmentTier: string) {
        if (input.filter.equipmentTier.includes(equipmentTier)) {
            input.filter.equipmentTier = input.filter.equipmentTier.filter(e => e !== equipmentTier);
        } else {
            input.filter.equipmentTier.push(equipmentTier);
        }
        input.updateFilter(input.filter);
    }
    function isActive(equipmentTier: string) {
        return input.filter.equipmentTier.includes(equipmentTier) ? " active" : "";
    }

    return (
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    <label>Bc trang b</label>
                    <div className="row">
                        <div className="col-sm-12" style={{ marginTop: "-5px" }}>
                            <button type="button" className={"btn btn-secondary" + (isActive("Normal"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateFilter("Normal");
                            }}>
                                <span className='d2-icon d2-tier-normal'></span>Bnh Thng
                            </button>
                            <button type="button" className={"btn btn-secondary" + (isActive("Exceptional"))} style={{ marginRight: "5px", marginTop: "5px" }} onClick={(event) => {
                                updateFilter("Exceptional");
                            }}>
                               <span className='d2-icon d2-tier-exceptional'></span>Xut Sc
                            </button>
                            <button type="button" className={"btn btn-secondary" + (isActive("Elite"))} style={{ marginTop: "5px" }} onClick={(event) => {
                                updateFilter("Elite");
                            }}>
                                <span className='d2-icon d2-tier-elite'></span>Tinh Ty
                            </button>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}