import React from 'react';

import { EquipmentSetBonus } from '../../../../../../../models/Diablo2Models';
import EquipmentSetListBonusItem from './EquipmentSetListBonusItem';

export default function EquipmentSetListBonus(input: { setBonuses: EquipmentSetBonus[] }) {
    const chunkSize = 3;
    const rows = [];
    for (let i = 0; i < input.setBonuses.length; i += chunkSize) {
        const chunk = input.setBonuses.slice(i, i + chunkSize);
        if (chunk.length === 3) {
            rows.push((
                <div className="row">
                    <div className="col-sm-4 text-center">
                        <EquipmentSetListBonusItem setBonus={chunk[0]} />
                    </div>
                    <div className="col-sm-4 text-center border-left">
                        <EquipmentSetListBonusItem setBonus={chunk[1]} />
                    </div>
                    <div className="col-sm-4 text-center border-left">
                        <EquipmentSetListBonusItem setBonus={chunk[2]} />
                    </div>
                </div>
            ));
        }

        if (chunk.length === 2) {
            rows.push((
                <div className="row">
                    <div className="col-sm-6 text-center">
                        <EquipmentSetListBonusItem setBonus={chunk[0]} />
                    </div>
                    <div className="col-sm-6 text-center border-left">
                        <EquipmentSetListBonusItem setBonus={chunk[1]} />
                    </div>
                </div>
            ));
        }

        if (chunk.length === 1) {
            rows.push((
                <div className="row">
                    <div className="col-sm-12 text-center">
                        <EquipmentSetListBonusItem setBonus={chunk[0]} />
                    </div>
                </div>
            ));
        }
    };

    return (
        <>{rows}</>
    );
}