import React from 'react';

export default function DataEquipmentBaseListCardPropBlock(input: {
    normalValue: string | undefined,
    exceptionalValue: string | undefined,
    eliteValue: string | undefined,
    textColor: string
}) {
    if ((!input.normalValue || input.normalValue === "")
        && (!input.exceptionalValue || input.exceptionalValue === "")
        && (!input.eliteValue || input.eliteValue === "")) {
        return (<></>);
    }

    return (
        <div className="row" style={{ color: input.textColor }}>
            <div className="col-5" style={{ textAlign: "left", paddingLeft: "5%" }}>
                T l  n
            </div>
            <div className="col-2">
                {
                    !input.normalValue || input.normalValue === ""
                        ? "-"
                        : (<>
                            Pal: {input.normalValue.split(" | ")[0]} <br />
                            Ama | Asn | Bar: {input.normalValue.split(" | ")[1]} <br />
                            Dru | Nec | Sor: {input.normalValue.split(" | ")[2]}
                        </>)
                }
            </div>
            <div className="col-2">
                {
                    !input.exceptionalValue || input.exceptionalValue === ""
                        ? "-"
                        : (<>
                            Pal: {input.exceptionalValue.split(" | ")[0]} <br />
                            Ama | Asn | Bar: {input.exceptionalValue.split(" | ")[1]} <br />
                            Dru | Nec | Sor: {input.exceptionalValue.split(" | ")[2]}
                        </>)
                }
            </div>
            <div className="col-2">
                {
                    !input.eliteValue || input.eliteValue === ""
                        ? "-"
                        : (<>
                            Pal: {input.eliteValue.split(" | ")[0]} <br />
                            Ama | Asn | Bar: {input.eliteValue.split(" | ")[1]} <br />
                            Dru | Nec | Sor: {input.eliteValue.split(" | ")[2]}
                        </>)
                }
            </div>
        </div>
    );
}