import React from 'react';

import { CupeRepiceIngredient } from '../../models/Diablo2Models';
import { Diablo2RuneDataService, useDiablo2RuneDataService } from '../../services/Diablo2RuneDataService';
import { Diablo2GemDataService, useDiablo2GemDataService } from '../../services/Diablo2GemDataService';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../services/Diablo2LocalizationService';

const diablo2RuneDataService: Diablo2RuneDataService = useDiablo2RuneDataService();
const diablo2GemDataService: Diablo2GemDataService = useDiablo2GemDataService();
const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export default function CupeRepiceIngredientText(input: {
    cupeRepiceIngredient: CupeRepiceIngredient
}) {
    const itemKey = input.cupeRepiceIngredient.itemKey;
    const itemQuantity = input.cupeRepiceIngredient.quantity;

    var itemName = "";

    // get rune name
    if (itemKey.startsWith("rune:")) {
        itemName = diablo2RuneDataService.getByKey(itemKey.replace("rune:", "")).name;
    }

    // get gem name
    if (itemKey.startsWith("gem:")) {
        itemName = diablo2GemDataService.getByKey(itemKey.replace("gem:", "")).nameKey;
        itemName = diablo2LocalizationService.getGemName(itemName);
    }

    return (
        <span>{itemQuantity} x {itemName}</span>
    );
}