import React from 'react';

import { Runeword } from '../../../../../models/Diablo2Models';
import DataRunewordListCard from "./DataRunewordListCard";


export default function DataRunewordList(input: {
    data: Runeword[]
}) {
    return (
        <div className="col-lg-7">
            {
                input.data.map((runeword) => {
                    return <DataRunewordListCard runeword={runeword} />
                })
            }
        </div>
    );
}