import React from 'react';

import ModifierText from '../ModifierText';

import { EquipmentSetBonus, EquipmentSetPart, EquipmentSetPartBase, EquipmentSetPartBaseProps } from '../../../models/Diablo2Models';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../services/Diablo2LocalizationService';
import { Diablo2EquipmentSetDataService, useDiablo2EquipmentSetDataService } from '../../../services/Diablo2EquipmentSetDataService';
import { Diablo2EquipmentBaseDataService, useDiablo2EquipmentBaseDataService } from '../../../services/Diablo2EquipmentBaseDataService';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();
const diablo2EquipmentSetDataService: Diablo2EquipmentSetDataService = useDiablo2EquipmentSetDataService();
const diablo2EquipmentBaseDataService: Diablo2EquipmentBaseDataService = useDiablo2EquipmentBaseDataService();

function SetPartBase(input: { partBase: EquipmentSetPartBase }) {
    var tier = ""
    if (input.partBase.tier) {
        tier = " - " + diablo2LocalizationService.getEquipmentTier(input.partBase.tier);
    }
    return (
        <div className="row text-white">
            <div className="col-12">
                Phi: <img
                    src={diablo2EquipmentBaseDataService.getEquipmentBaseImageUrl(input.partBase.image)}
                    alt={input.partBase.key}
                    width="20px"
                    style={{ maxWidth: "20px", maxHeight: "20px", objectFit: "cover", objectPosition: "top" }} /> {diablo2LocalizationService.getEquipmentName(input.partBase.nameKey)}{tier}
            </div>
        </div>
    );
}

function SetPartBaseWithoutImage(input: { partBase: EquipmentSetPartBase }) {
    var tier = ""
    if (input.partBase.tier) {
        tier = " - " + diablo2LocalizationService.getEquipmentTier(input.partBase.tier);
    }
    return (
        <div className="row text-white">
            <div className="col-12">
                Phi: {diablo2LocalizationService.getEquipmentName(input.partBase.nameKey)}{tier}
            </div>
        </div>
    );
}

function SetPartBaseProps(input: {
    baseProps: EquipmentSetPartBaseProps
}) {
    return (
        <div className='text-white'>
            <SetPartBaseProp title='Phng th' value={input.baseProps.defense} textColor="#84b7ff" />
            <SetPartBaseProp title='St thng mt tay' value={input.baseProps.oneHandDamage} textColor="#84b7ff" />
            <SetPartBaseProp title='St thng hai tay' value={input.baseProps.twoHandDamage} textColor="#84b7ff" />
            <SetPartBaseProp title='St thng mt tay (Ngi Man Di)' value={input.baseProps.oneHandDamageForBar} textColor="#84b7ff" />
            <SetPartBaseProp title='Tc  c bn' value={input.baseProps.baseSpeed} textColor="#84b7ff" />
            <SetPartBaseProp title=' bn' value={input.baseProps.durability} textColor="#84b7ff" />
            <SetPartBaseProp title='Yu cu sc mnh' value={input.baseProps.strengthRequired} textColor="#84b7ff" />
            <SetPartBaseProp title='Yu cu nhanh nhn' value={input.baseProps.dexterityRequired} textColor="#84b7ff" />
            <SetPartBaseProp title='Yu cu cp' value={input.baseProps.clvl} textColor="#84b7ff" />
            <SetPartBaseProp title='Cp cht lng' value={input.baseProps.qlvl} textColor="#84b7ff" />
            <SetPartBaseProp title='St thng ' value={input.baseProps.kickDamage} textColor="#84b7ff" />
            <SetPartBaseProp title='S  tht lng' value={input.baseProps.maxPotionSlot} textColor="#84b7ff" />
            <SetPartBaseProp title=' n' value={input.baseProps.block} textColor="#84b7ff" />
            {
                input.baseProps.classBlock
                    ? (
                        <div className="row">
                            <div className="col-sm-12">
                                 n theo lp nhn vt: <span style={{ color: "#84b7ff" }}>{input.baseProps.classBlock[0]}<br />
                                    {input.baseProps.classBlock[1]}<br />
                                    {input.baseProps.classBlock[2]}</span>
                            </div>
                        </div>
                    )
                    : (<></>)
            }
            <SetPartBaseProp title='St thng p' value={input.baseProps.smiteDamage} textColor="#84b7ff" />
            <div className="row">
                <div className="col-sm-12" style={{ color: "#ff7353" }}>
                    {input.baseProps.onlyClass
                        ? diablo2LocalizationService.getOnlyClassModifier(input.baseProps.onlyClass)
                        : ""}
                </div>
            </div>

        </div>
    )
}

function SetPartBaseProp(input: {
    title: string,
    value: string | number | undefined,
    textColor: string
}) {
    if (!input.value || input.value === "") {
        return (<></>);
    }

    return (
        <div className="row">
            <div className="col-sm-12">
                {input.title}: <span style={{ color: input.textColor }}>{input.value}</span>
            </div>
        </div>
    );
}

function SetPartBonus(input: {
    partBonus: EquipmentSetBonus,
    allParts: EquipmentSetPart[]
}) {
    var numberOfPartText = "(khi c " + input.partBonus.numberOfPart + " trang b"
    if (input.partBonus.specificParts) {
        numberOfPartText = numberOfPartText + ", m trong  c " + input.partBonus.specificParts.map((aPartKey) => {
            var part = input.allParts.filter((aEquipmentPart) => {
                return aEquipmentPart.key === aPartKey;
            })[0]
            return diablo2LocalizationService.getEquipmentName(part.nameKey);
        }).join(", ")
    }
    numberOfPartText = numberOfPartText.replaceAll("all", "tt c") + ")";
    return (
        <div className='text-set'>
            {
                input.partBonus.modifiers.map((modifier, index) => {
                    const isLast = input.partBonus.modifiers.length === (index + 1);
                    return !isLast
                        ? <><ModifierText modifier={modifier} textOnly={false} /> {numberOfPartText}<br /></>
                        : <><ModifierText modifier={modifier} textOnly={false} /> {numberOfPartText}</>;
                })
            }
        </div>
    )
}

export default function EquipmentSetPartFullInfo(input: { setKey: string, setPartKey: string }) {
    const validSet = diablo2EquipmentSetDataService.getByKey(input.setKey);
    const validPart = validSet.parts?.filter((aPart) => aPart.key === input.setPartKey)[0];
    if (!validPart) {
        return (
            <>ang cp nht</>
        )
    }

    return (
        <>
            <div className="row">
                <div className="col-12">
                    <div className='scale-part-img-container center-block'>
                        <img
                            src={diablo2EquipmentSetDataService.getEquipmentSetImageUrl(validPart.image)}
                            alt={validPart.key}
                            className='scale-part-img' />
                    </div>

                </div>
            </div>
            <div className="row">
                <div className="col-12 text-set">
                    {diablo2LocalizationService.getEquipmentName(validPart.nameKey)}
                </div>
            </div>
            {
                ["Charm", "Ring", "Amulet"].includes(validPart.base.type)
                    ? <SetPartBaseWithoutImage partBase={validPart.base} />
                    : <SetPartBase partBase={validPart.base} />
            }
            <SetPartBaseProps baseProps={validPart.baseProps} />
            <div style={{ color: "#84b7ff" }}>
                {
                    validPart.modifiers.map((modifier, index) => {
                        const isLast = validPart.modifiers.length === (index + 1);
                        return !isLast
                            ? <><ModifierText modifier={modifier} textOnly={false} /><br /></>
                            : <ModifierText modifier={modifier} textOnly={false} />;
                    })
                }
            </div>
            <div style={{ marginTop: "8px" }}>
                {
                    validPart.setBonus?.map((aSetBonus, index) => {
                        const isLast = validPart.setBonus?.length === (index + 1);
                        return !isLast
                            ? <><SetPartBonus partBonus={aSetBonus} allParts={validSet.parts ? validSet.parts : []} /></>
                            : <SetPartBonus partBonus={aSetBonus} allParts={validSet.parts ? validSet.parts : []} />;
                    })
                }
            </div>
        </>
    );
}