import React from 'react';

import EquipmentSetName from '../../../../../../compoments/diablo2/build/EquipmentSetName';
import EquipmentSetPartName from '../../../../../../compoments/diablo2/build/EquipmentSetPartName';
import EquipmentSetPartFullInfo from '../../../../../../compoments/diablo2/build/EquipmentSetPartFullInfo';
import EquipmentUniqueName from '../../../../../../compoments/diablo2/build/EquipmentUniqueName';
import EquipmentUniqueFullInfo from '../../../../../../compoments/diablo2/build/EquipmentUniqueFullInfo';

export default function EquipmentBelt() {
    return (
        <div id="equipment_belt_accordion">
        <div className="card skill-panel" >
            <a className="d-block w-100" data-toggle="collapse" href="#collapse_belt">
                <div className="card-header">
                    <h4 className="card-title w-100 skill-panel-title">Tht Lng</h4>
                </div>
            </a>
            <div id="collapse_belt" className="collapse skill-panel-content text-content" data-parent="#equipment_belt_accordion">
                <div className="card-body">
                    Nu bn mun trang b c b <EquipmentSetName setKey='TalRashasWrappings' /> th y s l ni dnh cho <EquipmentSetPartName setKey='TalRashasWrappings' setPartKey='TalRashasFineSpunCloth' />.
                    Nu khng th bn c th trang b <EquipmentUniqueName uniqueKey='ArachnidMesh' />   tng
                    <b><span className='text-white'> K Nng </span></b> v <b><span className='text-white'> Tc  Ra Chiu</span></b>.
                    Ngoi ra, bn cng c th trang b <EquipmentUniqueName uniqueKey='Snowclash' /> 
                    tng nhng <b><span className='text-white'> K Nng Chnh ca mnh </span></b> v <b><span className='text-white'> St Thng Bng C Bn</span></b>.
                    <div className="d-flex justify-content-center">
                        <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                            <EquipmentSetPartFullInfo setKey='TalRashasWrappings' setPartKey='TalRashasFineSpunCloth' />
                        </div>
                    </div>
                    <div className="d-flex justify-content-center">
                        <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                            <EquipmentUniqueFullInfo uniqueKey='ArachnidMesh' />
                        </div>
                    </div>
                    <div className="d-flex justify-content-center">
                        <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                            <EquipmentUniqueFullInfo uniqueKey='Snowclash' />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
} 