import _equipmentSet from '../assets/diablo2/database/equipment-set.json';
import { EquipmentSet } from '../models/Diablo2Models'

const equipmentSetData = _equipmentSet as EquipmentSet[];

export type Diablo2EquipmentSetDataService = ReturnType<typeof useDiablo2EquipmentSetDataService>;

export const useDiablo2EquipmentSetDataService = () => {

    function getByKey(setKey: string) {
        return equipmentSetData.filter((aSet) => {
            return aSet.key === setKey;
        })[0];
    }

    function getAll() {
        return equipmentSetData;
    }

    function getAllByTier(tier: string) {
        return equipmentSetData.filter((aEquipmentSet) => {
            return aEquipmentSet.tier === tier;
        })
    }

    function getEquipmentSetImageUrl(imageUrl: string) {
        return "/images/diablo2/equipment/set/" + imageUrl;
    }

    return {
        getByKey,
        getAll,
        getAllByTier,
        getEquipmentSetImageUrl,
    }
}