import React from 'react';

export default function PageContentHeader(input: {
    headerText: string,
    headerMapItems: string[],
}) {
    return (
        <section className="content-header">
            <div className="container">
                <div className="row mb-2">
                    <div className="col-sm-6 text-white">
                        <h1>{input.headerText}</h1>
                    </div>
                    <div className="col-sm-6">
                        <ol className="breadcrumb float-sm-right">
                            {input.headerMapItems.map((item, index) => {
                                const isLast = input.headerMapItems.length === (index + 1);
                                return !isLast 
                                ? <li className="breadcrumb-item"><a href="#">{item}</a></li> 
                                : <li className="breadcrumb-item active">{item}</li>;
                            })}
                        </ol>
                    </div>
                </div>
            </div>
        </section>
    );
}