import React from 'react';

import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../../services/Diablo2LocalizationService';

const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export default function DataEquipmentBaseListCardPropClassSpeed(input: {
    normalValue: string[] | undefined,
    exceptionalValue: string[] | undefined,
    eliteValue: string[] | undefined,
    textColor: string
}) {
    if (!input.normalValue && !input.exceptionalValue && !input.eliteValue) {
        return (<></>);
    }

    return (
        <div className="row" style={{ color: input.textColor }}>
            <div className="col-5" style={{ textAlign: "left", paddingLeft: "5%" }}>
                Tc  theo lp nhn vt
            </div>
            <div className="col-2">
                {
                    !input.normalValue
                        ? "-"
                        : input.normalValue.map((aValue, index) => {
                            const isLast = input.normalValue?.length === (index + 1);
                            return !isLast
                                ? <>- {aValue.split(": ")[0] + ": " + diablo2LocalizationService.getEquipmentClassSpeed(aValue.split(": ")[1])}<br /></>
                                : <>- {aValue.split(": ")[0] + ": " + diablo2LocalizationService.getEquipmentClassSpeed(aValue.split(": ")[1])}</>
                        })
                }
            </div>
            <div className="col-2">
                {
                    !input.exceptionalValue
                        ? "-"
                        : input.exceptionalValue.map((aValue, index) => {
                            const isLast = input.exceptionalValue?.length === (index + 1);
                            return !isLast
                                ? <>- {aValue.split(": ")[0] + ": " + diablo2LocalizationService.getEquipmentClassSpeed(aValue.split(": ")[1])}<br /></>
                                : <>- {aValue.split(": ")[0] + ": " + diablo2LocalizationService.getEquipmentClassSpeed(aValue.split(": ")[1])}</>
                        })
                }
            </div>
            <div className="col-2">
                {
                    !input.eliteValue
                        ? "-"
                        : input.eliteValue.map((aValue, index) => {
                            const isLast = input.eliteValue?.length === (index + 1);
                            return !isLast
                                ? <>- {aValue.split(": ")[0] + ": " + diablo2LocalizationService.getEquipmentClassSpeed(aValue.split(": ")[1])}<br /></>
                                : <>- {aValue.split(": ")[0] + ": " + diablo2LocalizationService.getEquipmentClassSpeed(aValue.split(": ")[1])}</>
                        })
                }
            </div>
        </div>
    );
}