import _runeWordData from '../assets/diablo2/database/runeword.json';
import { Runeword } from '../models/Diablo2Models'
import { Diablo2LocalizationService, useDiablo2LocalizationService } from './Diablo2LocalizationService';

const runewordData = _runeWordData as Runeword[];
const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();
export interface RunewordFilterRequest {
    keyword: string;
    numberSlot: string;
    fromLevel: number;
    toLevel: number;
    equipmentTypes: string[];
    sortBy: string;
}

export type Diablo2RunewordDataService = ReturnType<typeof useDiablo2RunewordDataService>;

export const useDiablo2RunewordDataService = () => {

    function getAll() {
        return runewordData;
    }

    function getByKey(runewordKey: string) {
        return runewordData.filter((aRune) => {
            return aRune.key === runewordKey;
        })[0];
    }

    function getByFilter(filterRequest: RunewordFilterRequest) {
        // filter by key
        if (filterRequest.keyword.startsWith("#")) {
            const runeword = getByKey(filterRequest.keyword.replace("#", ""));
            return runeword ? [runeword] : [];
        }

        // filter by request
        return runewordData.filter((aRune) => {
            var isMatch = true;

            // by name
            if (filterRequest.keyword) {                
                isMatch = isMatch && diablo2LocalizationService.getRunewordName(aRune.nameKey)
                    .toLowerCase()
                    .includes(filterRequest.keyword.toLowerCase());
            }

            // by number required slot
            if (filterRequest.numberSlot && Number(filterRequest.numberSlot)) {
                isMatch = isMatch && aRune.runes.length === Number(filterRequest.numberSlot);
            }

            // by from level
            if (filterRequest.fromLevel) {
                isMatch = isMatch && aRune.clvl >= filterRequest.fromLevel;
            }

            // by to level
            if (filterRequest.toLevel) {
                isMatch = isMatch && aRune.clvl <= filterRequest.toLevel;
            }

            // by equipment type
            if (filterRequest.equipmentTypes && filterRequest.equipmentTypes.length > 0) {
                const runewordBaseClasses = aRune.modifiersForBase.flatMap((aModifiersForBase) => {
                    return aModifiersForBase.baseClasses;
                })

                var maxRequiredSocketForBase = 6;
                if (filterRequest.equipmentTypes.includes("Weapon")
                    || filterRequest.equipmentTypes.includes("Melee Weapon")
                    || filterRequest.equipmentTypes.includes("Missile Weapon")
                    || filterRequest.equipmentTypes.includes("Polearm")
                    || filterRequest.equipmentTypes.includes("Spear")
                    || filterRequest.equipmentTypes.includes("Axe")
                    || filterRequest.equipmentTypes.includes("Sword")
                    || filterRequest.equipmentTypes.includes("Hammer")
                    || filterRequest.equipmentTypes.includes("Staff")) {
                    maxRequiredSocketForBase = 6;
                } else if (filterRequest.equipmentTypes.includes("Scepter")
                    || filterRequest.equipmentTypes.includes("Mace")) {
                    maxRequiredSocketForBase = 5;
                } else if (filterRequest.equipmentTypes.includes("Body Armor")
                    || filterRequest.equipmentTypes.includes("Shield")
                    || filterRequest.equipmentTypes.includes("Paladin Shield")) {
                    maxRequiredSocketForBase = 4;
                } else if (filterRequest.equipmentTypes.includes("Katar")
                    || filterRequest.equipmentTypes.includes("Club")
                    || filterRequest.equipmentTypes.includes("Dagger")
                    || filterRequest.equipmentTypes.includes("Helm")) {
                    maxRequiredSocketForBase = 3;
                } else if (filterRequest.equipmentTypes.includes("Wand")) {
                    maxRequiredSocketForBase = 2;
                }

                isMatch = isMatch && aRune.runes.length <= maxRequiredSocketForBase

                var enrichedFilterBaseClasses = filterRequest.equipmentTypes.slice();
                if (enrichedFilterBaseClasses.includes("Melee Weapon")
                    || enrichedFilterBaseClasses.includes("Polearm")
                    || enrichedFilterBaseClasses.includes("Spear")
                    || enrichedFilterBaseClasses.includes("Axe")
                    || enrichedFilterBaseClasses.includes("Sword")
                    || enrichedFilterBaseClasses.includes("Hammer")
                    || enrichedFilterBaseClasses.includes("Staff")
                    || enrichedFilterBaseClasses.includes("Scepter")
                    || enrichedFilterBaseClasses.includes("Mace")
                    || enrichedFilterBaseClasses.includes("Katar")
                    || enrichedFilterBaseClasses.includes("Club")
                    || enrichedFilterBaseClasses.includes("Dagger")
                    || enrichedFilterBaseClasses.includes("Wand")) {

                    if (!enrichedFilterBaseClasses.includes("Weapon")) {
                        enrichedFilterBaseClasses.push("Weapon");
                    }

                    if (!enrichedFilterBaseClasses.includes("Melee Weapon")) {
                        enrichedFilterBaseClasses.push("Melee Weapon");
                    }
                }

                if (enrichedFilterBaseClasses.includes("Missile Weapon")
                    && !enrichedFilterBaseClasses.includes("Weapon")) {
                    enrichedFilterBaseClasses.push("Weapon");
                }

                if (enrichedFilterBaseClasses.includes("Paladin Shield")
                    && !enrichedFilterBaseClasses.includes("Shield")) {
                    enrichedFilterBaseClasses.push("Shield");
                }

                isMatch = isMatch && enrichedFilterBaseClasses.map((baseClass) => {
                    return runewordBaseClasses.includes(baseClass);
                }).includes(true)
            }

            return isMatch;
        }).sort((aRune, otherRune) => {
            if (filterRequest.sortBy.startsWith("name|")) {
                return filterRequest.sortBy.endsWith("|asc")
                    ? diablo2LocalizationService.getRunewordName(aRune.nameKey)
                        .localeCompare(diablo2LocalizationService.getRunewordName(otherRune.nameKey))
                    : diablo2LocalizationService.getRunewordName(otherRune.nameKey)
                        .localeCompare(diablo2LocalizationService.getRunewordName(aRune.nameKey))
            }
            return filterRequest.sortBy.endsWith("|asc")
                ? aRune.clvl - otherRune.clvl
                : otherRune.clvl - aRune.clvl
        })
    }

    return {
        getAll,
        getByKey,
        getByFilter,
    }
}
