import _runeData from '../assets/diablo2/database/rune.json';
import { Rune } from '../models/Diablo2Models'

const runeData = _runeData as Rune[];

export type Diablo2RuneDataService = ReturnType<typeof useDiablo2RuneDataService>;

export const useDiablo2RuneDataService = () => {

    function getAll() {
        return runeData;
    }

    function getByKey(runeKey: string) {
        return runeData.filter((aRune) => {
            return aRune.key === runeKey;
        })[0];
    }

    function getRuneImageUrl(rune: Rune) {
        return "/images/diablo2/rune/" + rune.image;
    }

    return {
        getAll,
        getByKey,
        getRuneImageUrl,
    }
}
