import React from 'react';

import { Diablo2RunewordDataService, useDiablo2RunewordDataService } from '../../../services/Diablo2RunewordDataService';
import { Diablo2LocalizationService, useDiablo2LocalizationService } from '../../../services/Diablo2LocalizationService';

const diablo2RunewordDataService: Diablo2RunewordDataService = useDiablo2RunewordDataService();
const diablo2LocalizationService: Diablo2LocalizationService = useDiablo2LocalizationService();

export default function RunewordName(input: {runewordKey: string}) {
    const validRuneword = diablo2RunewordDataService.getByKey(input.runewordKey);

    return (
        <span style={{color:"#ecd2a8"}}><b>Ph Ng: {diablo2LocalizationService.getRunewordName(validRuneword.nameKey)}</b></span>
    );
}