import React from 'react';

import { EquipmentUniqueFilterRequest } from '../../../../../services/Diablo2EquipmentUniqueDataService';

import DataEquipmentUniqueFilterByName from './DataEquipmentUniqueFilterByName';
import DataEquipmentUniqueFilterByClvl from './DataEquipmentUniqueFilterByClvl';
import DataEquipmentUniqueFilterByTier from './DataEquipmentUniqueFilterByTier';
import DataEquipmentUniqueFilterByClass from './DataEquipmentUniqueFilterByClass';
import DataEquipmentUniqueFilterSort from './DataEquipmentUniqueFilterSort';

export default function DataEquipmentUniqueFilter(input: {
    filter: EquipmentUniqueFilterRequest,
    updateFilter: (filter: EquipmentUniqueFilterRequest) => void
}) {
    return (
        <div className="col-lg-5">
            <div className="card card-runeword-filter">
                <div className="card-header">
                    <h3 className="card-title">B lc</h3>
                </div>

                <div className="card-body bg-content text-content">
                    <form>
                        <DataEquipmentUniqueFilterByName filter={input.filter} updateFilter={input.updateFilter} />
                        <DataEquipmentUniqueFilterByClvl filter={input.filter} updateFilter={input.updateFilter} />
                        <DataEquipmentUniqueFilterByTier filter={input.filter} updateFilter={input.updateFilter} />
                        <DataEquipmentUniqueFilterByClass filter={input.filter} updateFilter={input.updateFilter} />
                        <DataEquipmentUniqueFilterSort filter={input.filter} updateFilter={input.updateFilter} />
                    </form>
                </div>
            </div>
        </div>
    );
}