import React from 'react';

export default function DataEquipmentBaseListCardProp(input: {
    title: string,
    normalValue: string | number | undefined,
    exceptionalValue: string | number | undefined,
    eliteValue: string | number | undefined,
    textColor: string
}) {
    if ((!input.normalValue || input.normalValue === "")
        && (!input.exceptionalValue || input.exceptionalValue === "")
        && (!input.eliteValue || input.eliteValue === "")) {
        return (<></>);
    }

    return (
        <div className="row" style={{ color: input.textColor }}>
            <div className="col-5" style={{ textAlign: "left", paddingLeft: "5%" }}>
                {input.title}
            </div>
            <div className="col-2">
                {!input.normalValue || input.normalValue === "" ? "-" : input.normalValue}
            </div>
            <div className="col-2">
                {!input.exceptionalValue || input.exceptionalValue === "" ? "-" : input.exceptionalValue}
            </div>
            <div className="col-2">
                {!input.eliteValue || input.eliteValue === "" ? "-" : input.eliteValue}
            </div>
        </div>
    );
}