import React from 'react';

import EquipmentUniqueName from '../../../../../../compoments/diablo2/build/EquipmentUniqueName';
import EquipmentUniqueFullInfo from '../../../../../../compoments/diablo2/build/EquipmentUniqueFullInfo';
import SkillName from '../../../../../../compoments/diablo2/build/SkillName';

export default function EquipmentBoot() {
    return (
        <div id="equipment_boot_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_boot">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">Giy</h4>
                    </div>
                </a>
                <div id="collapse_boot" className="collapse skill-panel-content text-content" data-parent="#equipment_boot_accordion">
                    <div className="card-body">
                        <EquipmentUniqueName uniqueKey='WarTraveler' /> hoc <EquipmentUniqueName uniqueKey='SandstormTrek' /> cng l mt la chn hon ho vi nhng cch build c lnh triu hi.<br />
                        <EquipmentUniqueName uniqueKey='Silkweave' /> cng l mt la chn khng ti, <span>+5 mana mi khi Git Qui Vt</span> kt hp vi k nng <SkillName skillKey='PoisonNova' /> s gip bn c  mana  s dng. <EquipmentUniqueName uniqueKey='Marrowwalk' /> cng kh n khi n <span>+10% tc  hi phc mana</span>.
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='WarTraveler' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='SandstormTrek' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='Silkweave' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='Marrowwalk' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}