import React from 'react';

import '../../../../assets/diablo2/css/d2.css'
import './DataGem.css'

import { Diablo2GemDataService, useDiablo2GemDataService } from '../../../../services/Diablo2GemDataService';

import PageContentHeader from '../../../../compoments/diablo2/PageContentHeader';
import DataGemTable from './DataGemTable';

const diablo2GemDataService: Diablo2GemDataService = useDiablo2GemDataService();

export default function DataGem() {
    return (
        <div className="content-wrapper bg-wrapper">
            <PageContentHeader headerText='Ngc' headerMapItems={["D Liu", "Ngc"]} />
            <section className="content">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="card">
                                <DataGemTable gems={diablo2GemDataService.getAll()} />
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}