import React from 'react';

import EquipmentSetPartName from '../../../../../../compoments/diablo2/build/EquipmentSetPartName';
import EquipmentSetPartFullInfo from '../../../../../../compoments/diablo2/build/EquipmentSetPartFullInfo';
import EquipmentUniqueName from '../../../../../../compoments/diablo2/build/EquipmentUniqueName';
import EquipmentUniqueFullInfo from '../../../../../../compoments/diablo2/build/EquipmentUniqueFullInfo';

export default function EquipmentGlove() {
    return (
        <div id="equipment_glove_accordion">
            <div className="card skill-panel" >
                <a className="d-block w-100" data-toggle="collapse" href="#collapse_glove">
                    <div className="card-header">
                        <h4 className="card-title w-100 skill-panel-title">Bao Tay</h4>
                    </div>
                </a>
                <div id="collapse_glove" className="collapse skill-panel-content text-content" data-parent="#equipment_glove_accordion">
                    <div className="card-body">
                        Bn c th trang b <EquipmentSetPartName setKey='Trang-OulsAvatar' setPartKey='Trang-OulsClaws' />  tng <b><span className='text-white'> Khng Bng</span></b>, gip bn chng li hiu ng ca Ba V Hiu Ha Min Dch Bng.
                        Hoc bn cng c th trang b <EquipmentUniqueName uniqueKey='Magefist' />  tng <b><span className='text-white'> Kh Nng Hi Phc Mana</span></b>.
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentSetPartFullInfo setKey='Trang-OulsAvatar' setPartKey='Trang-OulsClaws' />
                            </div>
                        </div>
                        <div className="d-flex justify-content-center">
                            <div style={{ backgroundColor: "black", width: "fit-content" }} className='text-center equipment-info-panel'>
                                <EquipmentUniqueFullInfo uniqueKey='Magefist' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}